import React, { useState, useEffect } from 'react';
import { 
  DollarSign, 
  Calendar, 
  Car, 
  Edit3, 
  CheckCircle, 
  XCircle,
  AlertCircle,
  TrendingUp
} from 'lucide-react';

interface Sale {
  id: number;
  car_name: string;
  make: string;
  model: string;
  year: string;
  listing_price: number;
  sale_price: number | null;
  sale_date: string;
  buyer_name: string | null;
  buyer_discord: string | null;
  sale_notes: string | null;
  sale_method: string;
  is_verified: boolean;
  dealer_name: string | null;
}

interface MySalesHistoryProps {
  userId: number;
}

const MySalesHistory: React.FC<MySalesHistoryProps> = ({ userId }) => {
  const [sales, setSales] = useState<Sale[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [editingSale, setEditingSale] = useState<number | null>(null);
  const [editAmount, setEditAmount] = useState<string>('');
  const [updating, setUpdating] = useState(false);

  useEffect(() => {
    fetchMySales();
  }, [userId]);

  const fetchMySales = async () => {
    try {
      setLoading(true);
      // In a real app, this would call the API
      // const response = await fetch(`/api/sales.php?action=my_sales&user_id=${userId}`);
      // const data = await response.json();
      
      // Mock data for demonstration
      const mockSales: Sale[] = [
        {
          id: 1,
          car_name: '2023 Sultan RS',
          make: 'Sultan',
          model: 'RS',
          year: '2023',
          listing_price: 85000,
          sale_price: 82000,
          sale_date: '2024-01-15T10:30:00Z',
          buyer_name: 'John Doe',
          buyer_discord: 'JohnDoe#1234',
          sale_notes: 'Sold through ((Discord))',
          sale_method: 'online',
          is_verified: true,
          dealer_name: 'Premium Motors'
        },
        {
          id: 2,
          car_name: '2022 Comet S2',
          make: 'Comet',
          model: 'S2',
          year: '2022',
          listing_price: 72500,
          sale_price: null,
          sale_date: '2024-01-20T14:15:00Z',
          buyer_name: null,
          buyer_discord: null,
          sale_notes: null,
          sale_method: 'online',
          is_verified: false,
          dealer_name: null
        }
      ];
      
      setSales(mockSales);
    } catch (err) {
      setError('Failed to fetch sales history');
    } finally {
      setLoading(false);
    }
  };

  const startEditing = (sale: Sale) => {
    setEditingSale(sale.id);
    setEditAmount(sale.sale_price?.toString() || '');
  };

  const cancelEditing = () => {
    setEditingSale(null);
    setEditAmount('');
  };

  const updateSaleAmount = async (saleId: number) => {
    if (!editAmount || isNaN(Number(editAmount))) {
      setError('Please enter a valid amount');
      return;
    }

    try {
      setUpdating(true);
      setError(null);

      // In a real app, this would call the API
      // const response = await fetch('/api/sales.php?action=update_sale_amount', {
      //   method: 'PUT',
      //   headers: { 'Content-Type': 'application/json' },
      //   body: JSON.stringify({
      //     sale_id: saleId,
      //     sale_price: parseFloat(editAmount),
      //     user_id: userId
      //   })
      // });
      
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));

      // Update local state
      setSales(prevSales => 
        prevSales.map(sale => 
          sale.id === saleId 
            ? { ...sale, sale_price: parseFloat(editAmount) }
            : sale
        )
      );

      setEditingSale(null);
      setEditAmount('');
    } catch (err) {
      setError('Failed to update sale amount');
    } finally {
      setUpdating(false);
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
        <div className="flex items-center space-x-2">
          <AlertCircle className="h-5 w-5 text-red-600" />
          <span className="text-red-700 dark:text-red-300">{error}</span>
        </div>
      </div>
    );
  }

  if (sales.length === 0) {
    return (
      <div className="text-center py-8">
        <Car className="h-12 w-12 text-gray-400 mx-auto mb-4" />
        <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">No Sales Yet</h3>
        <p className="text-gray-600 dark:text-gray-400">
          When you complete car sales, they'll appear here for tracking and editing.
        </p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white">My Sales History</h2>
          <p className="text-gray-600 dark:text-gray-400">
            Track and update your completed car sales
          </p>
        </div>
        <div className="flex items-center space-x-2 text-sm text-gray-600 dark:text-gray-400">
          <TrendingUp className="h-4 w-4" />
          <span>{sales.length} total sales</span>
        </div>
      </div>

      <div className="grid gap-4">
        {sales.map((sale) => (
          <div key={sale.id} className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-6">
            <div className="flex items-start justify-between mb-4">
              <div className="flex-1">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
                  {sale.car_name}
                </h3>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                  <div>
                    <span className="text-gray-600 dark:text-gray-400">Make/Model:</span>
                    <p className="font-medium dark:text-white">{sale.make} {sale.model}</p>
                  </div>
                  <div>
                    <span className="text-gray-600 dark:text-gray-400">Year:</span>
                    <p className="font-medium dark:text-white">{sale.year}</p>
                  </div>
                  <div>
                    <span className="text-gray-600 dark:text-gray-400">Listed Price:</span>
                    <p className="font-medium dark:text-white">{formatCurrency(sale.listing_price)}</p>
                  </div>
                  <div>
                    <span className="text-gray-600 dark:text-gray-400">Sale Date:</span>
                    <p className="font-medium dark:text-white">{formatDate(sale.sale_date)}</p>
                  </div>
                </div>
              </div>
              
              <div className="flex items-center space-x-2">
                {sale.is_verified ? (
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200">
                    <CheckCircle className="h-3 w-3 mr-1" />
                    Verified
                  </span>
                ) : (
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200">
                    <AlertCircle className="h-3 w-3 mr-1" />
                    Pending
                  </span>
                )}
              </div>
            </div>

            {/* Sale Details */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4 text-sm">
              {sale.buyer_name && (
                <div>
                  <span className="text-gray-600 dark:text-gray-400">Buyer:</span>
                  <p className="font-medium dark:text-white">{sale.buyer_name}</p>
                </div>
              )}
              {sale.buyer_discord && (
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">Buyer ((Discord)):</span>
                  <p className="font-medium dark:text-white">(({sale.buyer_discord}))</p>
                </div>
              )}
              {sale.dealer_name && (
                <div>
                  <span className="text-gray-600 dark:text-gray-400">Dealer:</span>
                  <p className="font-medium dark:text-white">{sale.dealer_name}</p>
                </div>
              )}
              {sale.sale_notes && (
                <div>
                  <span className="text-gray-600 dark:text-gray-400">Notes:</span>
                  <p className="font-medium dark:text-white">{sale.sale_notes}</p>
                </div>
              )}
            </div>

            {/* Sale Amount Section */}
            <div className="border-t border-gray-200 dark:border-gray-700 pt-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-3">
                  <DollarSign className="h-5 w-5 text-gray-400" />
                  <div>
                    <span className="text-sm text-gray-600 dark:text-gray-400">Sale Amount:</span>
                    {editingSale === sale.id ? (
                      <div className="flex items-center space-x-2 mt-1">
                        <input
                          type="number"
                          value={editAmount}
                          onChange={(e) => setEditAmount(e.target.value)}
                          className="w-24 px-2 py-1 text-sm border border-gray-300 dark:border-gray-600 rounded bg-white dark:bg-gray-700 text-gray-900 dark:text-white"
                          placeholder="0"
                          min="0"
                          step="100"
                        />
                        <button
                          onClick={() => updateSaleAmount(sale.id)}
                          disabled={updating}
                          className="px-3 py-1 text-xs bg-green-600 hover:bg-green-700 disabled:bg-green-400 text-white rounded transition-colors"
                        >
                          {updating ? 'Saving...' : 'Save'}
                        </button>
                        <button
                          onClick={cancelEditing}
                          disabled={updating}
                          className="px-3 py-1 text-xs bg-gray-500 hover:bg-gray-600 disabled:bg-gray-400 text-white rounded transition-colors"
                        >
                          Cancel
                        </button>
                      </div>
                    ) : (
                      <div className="flex items-center space-x-2 mt-1">
                        <span className="text-lg font-bold text-gray-900 dark:text-white">
                          {sale.sale_price ? formatCurrency(sale.sale_price) : 'Not set'}
                        </span>
                        <button
                          onClick={() => startEditing(sale)}
                          className="p-1 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300 transition-colors"
                          title="Edit sale amount"
                        >
                          <Edit3 className="h-4 w-4" />
                        </button>
                      </div>
                    )}
                  </div>
                </div>
                
                <div className="text-right">
                  {sale.sale_price && (
                    <div className="text-sm">
                      <span className="text-gray-600 dark:text-gray-400">Difference:</span>
                      <p className={`font-medium ${
                        sale.sale_price > sale.listing_price 
                          ? 'text-green-600 dark:text-green-400' 
                          : sale.sale_price < sale.listing_price 
                          ? 'text-red-600 dark:text-red-400'
                          : 'text-gray-600 dark:text-gray-400'
                      }`}>
                        {sale.sale_price > sale.listing_price ? '+' : ''}
                        {formatCurrency(sale.sale_price - sale.listing_price)}
                      </p>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Summary Stats */}
      <div className="bg-gray-50 dark:bg-gray-900 rounded-lg p-6">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Sales Summary</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="text-center">
            <div className="text-2xl font-bold text-primary-600 dark:text-primary-400">
              {sales.length}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">Total Sales</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-green-600 dark:text-green-400">
              {sales.filter(s => s.is_verified).length}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">Verified</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-yellow-600 dark:text-yellow-400">
              {sales.filter(s => !s.is_verified).length}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">Pending</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
              {sales.filter(s => s.sale_price).length}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">Amount Set</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MySalesHistory;
