import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Filter, Search, Car, Calendar, Gauge, Star } from 'lucide-react';
import { CarService, Car as CarType } from '../services/carService';
import { PaymentService } from '../services/paymentService';

const BrowseCars: React.FC = () => {
  const [filters, setFilters] = useState({
    priceRange: '',
    year: '',
    make: '',
    model: '',
    sellerType: '',
    mileage: '',
    search: '',
  });

  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');
  const [cars, setCars] = useState<CarType[]>([]);
  const [featuredCars, setFeaturedCars] = useState<CarType[]>([]);
  const [regularCars, setRegularCars] = useState<CarType[]>([]);
  const [filteredCars, setFilteredCars] = useState<CarType[]>([]);
  const [makes, setMakes] = useState<string[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadCars();
  }, []);

  useEffect(() => {
    applyFilters();
  }, [filters, cars]);

  const applyFilters = () => {
    let filtered = [...cars];
    
    // Apply search filter
    if (filters.search) {
      const searchTerm = filters.search.toLowerCase();
      filtered = filtered.filter(car => 
        car.make.toLowerCase().includes(searchTerm) ||
        car.model.toLowerCase().includes(searchTerm) ||
        car.description.toLowerCase().includes(searchTerm) ||
        car.title.toLowerCase().includes(searchTerm)
      );
    }
    
    // Apply other filters
    if (filters.make) {
      filtered = filtered.filter(car => car.make === filters.make);
    }
    
    if (filters.model) {
      filtered = filtered.filter(car => car.model.toLowerCase().includes(filters.model.toLowerCase()));
    }
    
    if (filters.year) {
      filtered = filtered.filter(car => car.year.toString() === filters.year);
    }
    
    if (filters.priceRange) {
      const [min, max] = filters.priceRange.split('-').map(Number);
      if (filters.priceRange === '100000+') {
        filtered = filtered.filter(car => car.price >= 100000);
      } else if (max) {
        filtered = filtered.filter(car => car.price >= min && car.price <= max);
      }
    }
    
    if (filters.sellerType) {
      if (filters.sellerType === 'dealer') {
        filtered = filtered.filter(car => car.dealer_id);
      } else if (filters.sellerType === 'private') {
        filtered = filtered.filter(car => !car.dealer_id);
      }
    }
    
    if (filters.mileage) {
      const maxMileage = parseInt(filters.mileage);
      filtered = filtered.filter(car => car.mileage && car.mileage <= maxMileage);
    }
    
    // Maintain featured cars priority in filtered results
    const filteredFeatured = filtered.filter(car => car.featured_status === 'active');
    const filteredRegular = filtered.filter(car => car.featured_status !== 'active');
    
    setFilteredCars([...filteredFeatured, ...filteredRegular]);
  };

  const handleFilterChange = (key: string, value: string) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  const handleSearchChange = (value: string) => {
    setFilters(prev => ({ ...prev, search: value }));
  };

  const loadCars = async () => {
    try {
      setLoading(true);
      
      // Load featured cars and regular cars in parallel
      const [featuredCarsData, regularCarsData] = await Promise.all([
        PaymentService.getFeaturedCars().catch(() => []), // Fallback to empty array if featured cars fail
        CarService.getCars()
      ]);
      
      setFeaturedCars(featuredCarsData);
      setRegularCars(regularCarsData);
      
      // Combine featured cars first, then regular cars
      const allCars = [...featuredCarsData, ...regularCarsData];
      setCars(allCars);
      
      // Extract unique makes from all cars data
      const uniqueMakes = Array.from(new Set(allCars.map(car => car.make)));
      setMakes(uniqueMakes);
    } catch (err) {
      console.error('Error loading cars:', err);
      setCars([]);
      setFeaturedCars([]);
      setRegularCars([]);
      setMakes([]);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">Browse Cars</h1>
          <p className="text-gray-600 dark:text-gray-300">Find your perfect ride from our extensive collection</p>
        </div>

        {/* Loading State */}
        {loading && (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mx-auto mb-4"></div>
            <p className="text-gray-600 dark:text-gray-300">Loading cars...</p>
          </div>
        )}

        {/* No Cars State */}
        {!loading && filteredCars.length === 0 && (
          <div className="text-center py-12">
            <div className="bg-gray-100 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-4 mb-4">
              <p className="text-gray-800 dark:text-gray-200 text-lg">
                {cars.length === 0 ? 'No cars to show...' : 'No cars match your current filters'}
              </p>
              {cars.length > 0 && (
                <button
                  onClick={() => setFilters({
                    priceRange: '',
                    year: '',
                    make: '',
                    model: '',
                    sellerType: '',
                    mileage: '',
                    search: '',
                  })}
                  className="mt-2 text-primary-600 hover:text-primary-700 dark:text-primary-400 dark:hover:text-primary-300 underline"
                >
                  Clear all filters
                </button>
              )}
            </div>
          </div>
        )}

        {!loading && (
          <div className="flex flex-col lg:flex-row gap-8">
            {/* Filters Sidebar */}
            <div className="lg:w-80">
            <div className="card sticky top-8">
              <div className="flex items-center space-x-2 mb-6">
                <Filter className="h-5 w-5 text-primary-600" />
                <h3 className="text-lg font-semibold">Filters</h3>
              </div>

              <div className="space-y-6">
                {/* Price Range */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-200 mb-2">Price Range</label>
                  <select
                    value={filters.priceRange}
                    onChange={(e) => handleFilterChange('priceRange', e.target.value)}
                    className="input-field"
                  >
                    <option value="">Any Price</option>
                    <option value="0-25000">Under $25,000</option>
                    <option value="25000-50000">$25,000 - $50,000</option>
                    <option value="50000-75000">$50,000 - $75,000</option>
                    <option value="75000-100000">$75,000 - $100,000</option>
                    <option value="100000+">Over $100,000</option>
                  </select>
                </div>

                {/* Year */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-200 mb-2">Year</label>
                  <select
                    value={filters.year}
                    onChange={(e) => handleFilterChange('year', e.target.value)}
                    className="input-field"
                  >
                    <option value="">Any Year</option>
                    <option value="2024">2024</option>
                    <option value="2023">2023</option>
                    <option value="2022">2022</option>
                    <option value="2021">2021</option>
                    <option value="2020">2020</option>
                    <option value="2019">2019</option>
                  </select>
                </div>

                {/* Make */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-200 mb-2">Make</label>
                  <select
                    value={filters.make}
                    onChange={(e) => handleFilterChange('make', e.target.value)}
                    className="input-field"
                  >
                    <option value="">Any Make</option>
                    {makes.map(make => (
                      <option key={make} value={make}>{make}</option>
                    ))}
                  </select>
                </div>

                {/* Model */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-200 mb-2">Model</label>
                  <input
                    type="text"
                    placeholder="Any Model"
                    value={filters.model}
                    onChange={(e) => handleFilterChange('model', e.target.value)}
                    className="input-field"
                  />
                </div>



                {/* Seller Type */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-200 mb-2">Seller Type</label>
                  <select
                    value={filters.sellerType}
                    onChange={(e) => handleFilterChange('sellerType', e.target.value)}
                    className="input-field"
                  >
                    <option value="">Any Seller</option>
                    <option value="dealer">Dealers Only</option>
                    <option value="private">Private Sellers Only</option>
                  </select>
                </div>

                {/* Mileage */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-200 mb-2">Max Mileage</label>
                  <select
                    value={filters.mileage}
                    onChange={(e) => handleFilterChange('mileage', e.target.value)}
                    className="input-field"
                  >
                    <option value="">Any Mileage</option>
                    <option value="10000">Under 10,000</option>
                    <option value="25000">Under 25,000</option>
                    <option value="50000">Under 50,000</option>
                    <option value="75000">Under 75,000</option>
                    <option value="100000">Under 100,000</option>
                  </select>
                </div>

                {/* Clear Filters */}
                <button
                  onClick={() => setFilters({
                    priceRange: '',
                    year: '',
                    make: '',
                    model: '',
                    sellerType: '',
                    mileage: '',
                    search: '', // Added search to clear
                  })}
                  className="w-full btn-secondary"
                >
                  Clear All Filters
                </button>
              </div>
            </div>
          </div>

          {/* Main Content */}
          <div className="flex-1">
            {/* Search and View Toggle */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-6">
              <div className="relative flex-1 max-w-md">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <input
                  type="text"
                  placeholder="Search cars by make, model, or description..."
                  value={filters.search}
                  onChange={(e) => handleSearchChange(e.target.value)}
                  className="pl-10 pr-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent w-full bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100 placeholder-gray-500 dark:placeholder-gray-400"
                />
              </div>
              
              <div className="flex items-center space-x-2">
                <span className="text-sm text-gray-600 dark:text-gray-300">View:</span>
                <button
                  onClick={() => setViewMode('grid')}
                  className={`p-2 rounded ${viewMode === 'grid' ? 'bg-primary-100 dark:bg-primary-900 text-primary-600 dark:text-primary-400' : 'text-gray-400 dark:text-gray-500 hover:text-gray-600 dark:hover:text-gray-300'}`}
                >
                  <div className="grid grid-cols-2 gap-1 w-4 h-4">
                    <div className="bg-current rounded-sm"></div>
                    <div className="bg-current rounded-sm"></div>
                    <div className="bg-current rounded-sm"></div>
                    <div className="bg-current rounded-sm"></div>
                  </div>
                </button>
                <button
                  onClick={() => setViewMode('list')}
                  className={`p-2 rounded ${viewMode === 'list' ? 'bg-primary-100 dark:bg-primary-900 text-primary-600 dark:text-primary-400' : 'text-gray-400 dark:text-gray-500 hover:text-gray-600 dark:hover:text-gray-300'}`}
                >
                  <div className="space-y-1 w-4 h-4">
                    <div className="bg-current rounded-sm h-1"></div>
                    <div className="bg-current rounded-sm h-1"></div>
                    <div className="bg-current rounded-sm h-1"></div>
                  </div>
                </button>
              </div>
            </div>

            {/* Results Count */}
            <div className="mb-6">
              <p className="text-gray-600 dark:text-gray-300">
                Showing <span className="font-medium">{filteredCars.length}</span> cars
              </p>
            </div>

            {/* Featured Cars Section */}
            {filteredCars.filter(car => car.featured_status === 'active').length > 0 && (
              <div className="mb-8">
                <div className="flex items-center space-x-2 mb-4">
                  <Star className="h-5 w-5 text-yellow-500" />
                  <h3 className="text-lg font-semibold text-gray-900 dark:text-white">Featured Cars</h3>
                  <span className="text-sm text-gray-500 dark:text-gray-400">
                    ({filteredCars.filter(car => car.featured_status === 'active').length})
                  </span>
                </div>
                <div className="bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800 rounded-lg p-4 mb-4">
                  <p className="text-sm text-yellow-800 dark:text-yellow-200">
                    These cars are currently featured and appear at the top of search results.
                  </p>
                </div>
              </div>
            )}

            {/* Cars Grid/List */}
            {viewMode === 'grid' ? (
              <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
                {filteredCars.map((car) => (
                  <CarCard key={car.id} car={car} />
                ))}
              </div>
            ) : (
              <div className="space-y-4">
                {filteredCars.map((car) => (
                  <CarListCard key={car.id} car={car} />
                ))}
              </div>
            )}
          </div>
        </div>
        )}
      </div>
    </div>
  );
};

// Car Card Component for Grid View
const CarCard: React.FC<{ car: CarType }> = ({ car }) => (
  <div className="card hover:shadow-lg transition-shadow duration-300">
    <div className="relative mb-4">
      <img
        src={car.images?.[0] || 'https://via.placeholder.com/400x300?text=No+Image'}
        alt={`${car.year} ${car.make} ${car.model}`}
        className="w-full h-48 object-cover rounded-lg"
      />
      {car.dealer_id && (
        <span className="absolute top-2 left-2 bg-primary-600 text-white text-xs px-2 py-1 rounded-full">
          Dealer
        </span>
      )}
      {car.featured_status === 'active' && (
        <span className="absolute top-2 right-2 bg-yellow-500 text-white text-xs px-2 py-1 rounded-full flex items-center">
          <Star className="h-3 w-3 mr-1" /> Featured
        </span>
      )}
    </div>
    <div className="space-y-3">
      <h3 className="text-xl font-semibold text-gray-900 dark:text-white">{car.year} {car.make} {car.model}</h3>
      <div className="flex items-center justify-between">
        <span className="text-2xl font-bold text-primary-600">${car.price.toLocaleString()}</span>
        <span className="text-sm text-gray-500 dark:text-gray-400">{car.year}</span>
      </div>
      <div className="text-sm text-gray-600 dark:text-gray-300 space-y-1">
        {car.mileage && (
          <div className="flex items-center space-x-1">
            <Gauge className="h-3 w-3" />
            <span>{car.mileage.toLocaleString()} miles</span>
          </div>
        )}
        <div className="flex items-center space-x-1">
          <Car className="h-3 w-3" />
          <Link 
            to={`/user/${car.seller_id}`}
            className="text-primary-600 dark:text-primary-400 font-medium hover:underline"
          >
            Seller #{car.seller_id}
          </Link>
        </div>
      </div>
      <Link
        to={`/car/${car.id}`}
        className="btn-primary w-full text-center block"
      >
        View Details
      </Link>
    </div>
  </div>
);

// Car List Card Component for List View
const CarListCard: React.FC<{ car: CarType }> = ({ car }) => (
  <div className="card hover:shadow-lg transition-shadow duration-300">
    <div className="flex flex-col md:flex-row gap-4">
      <div className="relative md:w-48 md:flex-shrink-0">
        <img
          src={car.images?.[0] || 'https://via.placeholder.com/400x300?text=No+Image'}
          alt={`${car.year} ${car.make} ${car.model}`}
          className="w-full h-32 md:h-full object-cover rounded-lg"
        />
        {car.dealer_id && (
          <span className="absolute top-2 left-2 bg-primary-600 text-white text-xs px-2 py-1 rounded-full">
            Dealer
          </span>
        )}
        {car.featured_status === 'active' && (
          <span className="absolute top-2 right-2 bg-yellow-500 text-white text-xs px-2 py-1 rounded-full flex items-center">
            <Star className="h-3 w-3 mr-1" /> Featured
          </span>
        )}
      </div>
      <div className="flex-1">
        <div className="flex items-start justify-between mb-2">
          <div>
            <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-1">{car.year} {car.make} {car.model}</h3>
            <p className="text-gray-600 dark:text-gray-300 text-sm mb-2">{car.description}</p>
          </div>
          <div className="text-right">
            <div className="text-2xl font-bold text-primary-600">${car.price.toLocaleString()}</div>
          </div>
        </div>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm text-gray-600 dark:text-gray-300 mb-4">
          <div className="flex items-center space-x-1">
            <Calendar className="h-3 w-3" />
            <span>{car.year}</span>
          </div>
          {car.mileage && (
            <div className="flex items-center space-x-1">
              <Gauge className="h-3 w-3" />
              <span>{car.mileage.toLocaleString()} miles</span>
            </div>
          )}
          <div className="flex items-center space-x-1">
            <Car className="h-3 w-3" />
            <Link 
              to={`/user/${car.seller_id}`}
              className="text-primary-600 dark:text-primary-400 font-medium hover:underline"
            >
              Seller #{car.seller_id}
            </Link>
          </div>
        </div>
        <Link
          to={`/car/${car.id}`}
          className="btn-primary inline-block"
        >
          View Details
        </Link>
      </div>
    </div>
  </div>
);

export default BrowseCars;
