import { User } from '../contexts/UserContext';

export interface ProfileCompletionData {
  phone_number: string;
  routing_number: string;
  discord?: string | null;
}

export class ProfileService {
  // Check if user needs to complete their profile
  static needsProfileCompletion(user: User): boolean {
    return !user.phone_number || !user.routing_number;
  }

  // Check if user has staff access
  static hasStaffAccess(user: User): boolean {
    return !!user.staff_role;
  }

  // Complete user profile
  static async completeProfile(data: ProfileCompletionData, userId: number): Promise<{ success: boolean; message: string }> {
    try {
      const requestData = {
        ...data,
        user_id: userId
      };
      
      const response = await fetch('/api/auth/complete-profile', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to complete profile');
      }

      return await response.json();
    } catch (error) {
      console.error('Profile completion error:', error);
      throw error;
    }
  }

  // Validate phone number format - allow any number of digits
  static validatePhoneNumber(phone: string): boolean {
    const cleanPhone = phone.replace(/\D/g, '');
    return cleanPhone.length > 0; // Just ensure it's not empty and contains numbers
  }

  // Validate routing number format - Fleeca routing number (9 digits)
  static validateRoutingNumber(routing: string): boolean {
    return /^[0-9]{9}$/.test(routing);
  }

  // Validate Discord username format
  static validateDiscordUsername(discord: string): boolean {
    if (!discord) return true; // Optional field
    // Discord usernames are 2-32 characters, alphanumeric + underscore + period
    // No discriminator required anymore
    return /^[a-zA-Z0-9_.]{2,32}$/.test(discord);
  }

  // Format phone number for display - allow flexible input, just clean non-digits
  static formatPhoneNumber(value: string): string {
    // Just return the cleaned numbers, don't force specific formatting
    return value.replace(/\D/g, '');
  }
}

export default ProfileService;
