export interface Car {
  id?: number;
  title: string;
  make: string;
  model: string;
  year: number;
  price: number;
  description: string;
  seller_id: number;
  dealer_id?: number;
  status: 'active' | 'sold' | 'pending' | 'flagged' | 'removed';
  created_at?: Date;
  updated_at?: Date;
  images?: string[];
  mileage?: number;
  fuel_type?: string;
  transmission?: string;
  color?: string;
  vin?: string;
  views?: number;
  inquiries?: number;
  
  // Featured car properties (when car is featured)
  is_featured?: boolean;
  featured_start_time?: string | Date;
  featured_end_time?: string | Date;
  featured_status?: 'active' | 'expired' | 'cancelled';
}

export const CarService = {
  // Get all cars with optional filters
  async getCars(filters?: {
    make?: string;
    model?: string;
    year?: number;
    minPrice?: number;
    maxPrice?: number;
    status?: string;
    sellerId?: number;
    dealerId?: number;
  }): Promise<Car[]> {
    try {
      const queryParams = new URLSearchParams();
      
      if (filters?.make) queryParams.append('make', filters.make);
      if (filters?.model) queryParams.append('model', filters.model);
      if (filters?.year) queryParams.append('year', filters.year.toString());
      if (filters?.minPrice) queryParams.append('minPrice', filters.minPrice.toString());
      if (filters?.maxPrice) queryParams.append('maxPrice', filters.maxPrice.toString());
      if (filters?.status) queryParams.append('status', filters.status);
      if (filters?.sellerId) queryParams.append('sellerId', filters.sellerId.toString());
      if (filters?.dealerId) queryParams.append('dealerId', filters.dealerId.toString());

      const response = await fetch(`/api/cars.php?${queryParams.toString()}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting cars:', error);
      throw new Error('Failed to get cars');
    }
  },

  // Get a single car by ID
  async getCarById(id: number): Promise<Car | null> {
    try {
      const response = await fetch(`/api/cars.php?id=${id}`);
      
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting car:', error);
      throw new Error('Failed to get car');
    }
  },

  // Create a new car listing
  async createCar(car: Omit<Car, 'id' | 'created_at' | 'updated_at'>): Promise<Car> {
    try {
      const response = await fetch('/api/cars.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(car),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const result = await response.json();
      return { ...car, id: result.id };
    } catch (error) {
      console.error('Error creating car:', error);
      throw new Error('Failed to create car');
    }
  },

  // Update a car listing
  async updateCar(id: number, updates: Partial<Car>): Promise<Car | null> {
    try {
      const response = await fetch('/api/cars.php', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ id, ...updates }),
      });
      
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error updating car:', error);
      throw new Error('Failed to update car');
    }
  },

  // Delete a car listing
  async deleteCar(id: number): Promise<boolean> {
    try {
      const response = await fetch(`/api/cars.php?id=${id}`, {
        method: 'DELETE',
      });
      
      if (!response.ok) {
        if (response.status === 404) return false;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error deleting car:', error);
      throw new Error('Failed to delete car');
    }
  },

  // Get cars by seller
  async getCarsBySeller(sellerId: number): Promise<Car[]> {
    try {
      const response = await fetch(`/api/cars.php?sellerId=${sellerId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting seller cars:', error);
      throw new Error('Failed to get seller cars');
    }
  },

  // Get cars by dealer
  async getCarsByDealer(dealerId: number): Promise<Car[]> {
    try {
      const response = await fetch(`/api/cars.php?action=dealer&dealerId=${dealerId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      
      if (data.success && data.cars) {
        return data.cars;
      } else {
        throw new Error(data.error || 'Failed to get dealer cars');
      }
    } catch (error) {
      console.error('Error getting dealer cars:', error);
      throw new Error('Failed to get dealer cars');
    }
  }
};
