import React, { useState } from 'react';
import { useUser } from '../contexts/UserContext';
import { ProfileService } from '../services/profileService';
import { User, Phone, Hash, MessageCircle, Edit3, Save, X, Shield, Store } from 'lucide-react';

const Profile: React.FC = () => {
  const { user, login } = useUser();
  const [isEditing, setIsEditing] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formData, setFormData] = useState({
    phone_number: user?.phone_number || '',
    routing_number: user?.routing_number || '',
    discord: user?.discord || '' as string | null
  });
  const [errors, setErrors] = useState<Record<string, string>>({});

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
        <div className="text-center">
          <p className="text-gray-600 dark:text-gray-400">Please log in to view your profile.</p>
        </div>
      </div>
    );
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value === '' ? null : value }));
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: '' }));
    }
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    // Phone number validation
    if (!formData.phone_number) {
      newErrors.phone_number = 'Phone number is required';
    } else if (!ProfileService.validatePhoneNumber(formData.phone_number)) {
             newErrors.phone_number = 'Please enter a valid phone number (any number of digits)';
    }

    // Routing number validation
    if (!formData.routing_number) {
      newErrors.routing_number = 'Routing number is required';
    } else if (!ProfileService.validateRoutingNumber(formData.routing_number)) {
             newErrors.routing_number = 'Please enter a valid routing number (9 digits)';
    }

    // Discord validation
    if (formData.discord && !ProfileService.validateDiscordUsername(formData.discord)) {
      newErrors.discord = 'Please enter a valid Discord username';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);

    try {
      const result = await ProfileService.completeProfile({
        phone_number: formData.phone_number.replace(/\D/g, ''),
        routing_number: formData.routing_number,
        discord: formData.discord || null
      }, user.id);
      
      if (result.success) {
        // Update user context with new information
        const updatedUser = {
          ...user,
          phone_number: formData.phone_number.replace(/\D/g, ''),
          routing_number: formData.routing_number,
          discord: formData.discord
        };
        await login(updatedUser);
        setIsEditing(false);
        setErrors({});
      }
    } catch (error) {
      console.error('Profile update error:', error);
      setErrors({ submit: error instanceof Error ? error.message : 'Failed to update profile' });
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => {
    setIsEditing(false);
    setFormData({
      phone_number: user.phone_number || '',
      routing_number: user.routing_number || '',
      discord: user.discord || null
    });
    setErrors({});
  };

  const handlePhoneChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    // Allow any input, just store the raw value
    setFormData(prev => ({ ...prev, phone_number: e.target.value }));
    if (errors.phone_number) {
      setErrors(prev => ({ ...prev, phone_number: '' }));
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-12 px-4">
      <div className="max-w-4xl mx-auto">
        <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-xl overflow-hidden">
          {/* Header */}
          <div className="bg-gradient-to-r from-primary-600 to-primary-700 px-6 py-8 text-white">
            <div className="flex items-center space-x-4">
              <div className="w-20 h-20 rounded-full bg-white/20 flex items-center justify-center">
                {user.avatar_url ? (
                  <img
                    src={user.avatar_url}
                    alt={user.name}
                    className="w-16 h-16 rounded-full object-cover"
                  />
                ) : (
                  <User className="h-10 w-10" />
                )}
              </div>
              <div>
                <h1 className="text-3xl font-bold">{user.name}</h1>
                <div className="flex items-center space-x-2 mt-2">
                  {user.staff_role && (
                    <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200">
                      <Shield className="h-4 w-4 mr-1" />
                      {user.staff_role}
                    </span>
                  )}
                  {user.is_dealer && (
                    <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200">
                      <Store className="h-4 w-4 mr-1" />
                      Dealer
                    </span>
                  )}
                </div>
              </div>
            </div>
          </div>

          {/* Content */}
          <div className="p-6">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-2xl font-bold text-gray-900 dark:text-white">Profile Information</h2>
              {!isEditing ? (
                <button
                  onClick={() => setIsEditing(true)}
                  className="btn-primary flex items-center space-x-2"
                >
                  <Edit3 className="h-4 w-4" />
                  <span>Edit Profile</span>
                </button>
              ) : (
                <div className="flex space-x-2">
                  <button
                    onClick={handleCancel}
                    className="btn-secondary flex items-center space-x-2"
                  >
                    <X className="h-4 w-4" />
                    <span>Cancel</span>
                  </button>
                  <button
                    onClick={handleSubmit}
                    disabled={isSubmitting}
                    className="btn-primary flex items-center space-x-2"
                  >
                    <Save className="h-4 w-4" />
                    <span>{isSubmitting ? 'Saving...' : 'Save Changes'}</span>
                  </button>
                </div>
              )}
            </div>

            {isEditing ? (
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Phone Number */}
                <div>
                  <label htmlFor="phone_number" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    <Phone className="h-4 w-4 inline mr-2" />
                    Phone Number
                  </label>
                  <input
                    type="tel"
                    id="phone_number"
                    name="phone_number"
                    value={formData.phone_number}
                    onChange={handlePhoneChange}
                                         placeholder="5551234567"
                    className={`w-full px-4 py-3 border rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-colors ${
                      errors.phone_number 
                        ? 'border-red-300 focus:ring-red-500' 
                        : 'border-gray-300 dark:border-gray-600'
                    } bg-white dark:bg-gray-700 text-gray-900 dark:text-white`}
                  />
                  {errors.phone_number && (
                    <p className="mt-1 text-sm text-red-600 dark:text-red-400">{errors.phone_number}</p>
                  )}
                </div>

                {/* Routing Number */}
                <div>
                  <label htmlFor="routing_number" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    <Hash className="h-4 w-4 inline mr-2" />
                    Routing Number
                  </label>
                  <input
                    type="text"
                    id="routing_number"
                    name="routing_number"
                    value={formData.routing_number}
                    onChange={handleInputChange}
                                         placeholder="123456789"
                                         maxLength={9}
                    className={`w-full px-4 py-3 border rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-colors ${
                      errors.routing_number 
                        ? 'border-red-300 focus:ring-red-500' 
                        : 'border-gray-300 dark:border-gray-600'
                    } bg-white dark:bg-gray-700 text-gray-900 dark:text-white`}
                  />
                  {errors.routing_number && (
                    <p className="mt-1 text-sm text-red-600 dark:text-red-400">{errors.routing_number}</p>
                  )}
                </div>

                {/* Discord */}
                <div>
                                                                          <label htmlFor="discord" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      <MessageCircle className="h-4 w-4 inline mr-2" />
                      ((Discord)) (Optional)
                    </label>
                  <input
                    type="text"
                    id="discord"
                    name="discord"
                    value={formData.discord || ''}
                    onChange={handleInputChange}
                    placeholder="username"
                    className={`w-full px-4 py-3 border rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-colors ${
                      errors.discord 
                        ? 'border-red-300 focus:ring-red-500' 
                        : 'border-gray-300 dark:border-gray-600'
                    } bg-white dark:bg-gray-700 text-gray-900 dark:text-white`}
                  />
                  {errors.discord && (
                    <p className="mt-1 text-sm text-red-600 dark:text-red-400">{errors.discord}</p>
                  )}
                </div>

                {/* Submit Error */}
                {errors.submit && (
                  <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
                    <p className="text-sm text-red-600 dark:text-red-400">{errors.submit}</p>
                  </div>
                )}
              </form>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">
                      <Phone className="h-4 w-4 inline mr-2" />
                      Phone Number
                    </label>
                    <p className="text-gray-900 dark:text-white">
                                             {user.phone_number || 'Not provided'}
                    </p>
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">
                      <Hash className="h-4 w-4 inline mr-2" />
                      Routing Number
                    </label>
                    <p className="text-gray-900 dark:text-white">
                      {user.routing_number || 'Not provided'}
                    </p>
                  </div>
                  
                                     <div>
                     <label className="block text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">
                       <MessageCircle className="h-4 w-4 inline mr-2" />
                       ((Discord))
                     </label>
                    <p className="text-gray-900 dark:text-white">
                      {user.discord || 'Not provided'}
                    </p>
                  </div>
                </div>
                
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">
                      Member Since
                    </label>
                    <p className="text-gray-900 dark:text-white">
                      {user.created_at ? new Date(user.created_at).toLocaleDateString() : 'Unknown'}
                    </p>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
