// Messaging service - Connected to PHP API backend
// This service will be updated to use PHP endpoints when they're created

export interface Message {
  id?: number;
  conversation_id: number;
  sender_id: number;
  content: string;
  message_type: 'text' | 'image' | 'file';
  created_at?: Date;
  updated_at?: Date;
}

export interface Conversation {
  id?: number;
  title?: string;
  conversation_type: 'direct' | 'group';
  created_at?: Date;
  updated_at?: Date;
  participants: ConversationParticipant[];
  messages: Message[];
}

export interface ConversationSummary {
  id: number;
  title?: string;
  conversation_type: 'direct' | 'group';
  created_at: Date;
  updated_at: Date;
  participants: ConversationParticipant[];
  last_message: {
    content: string;
    sender_id: number;
    created_at: Date;
  };
  unread_count: number;
}

export interface ConversationParticipant {
  id?: number;
  conversation_id: number;
  user_id: number;
  name: string;
  discord: string;
  avatar_url?: string;
  role?: string;
}

export interface MessageAttachment {
  id?: number;
  message_id: number;
  file_name: string;
  file_url: string;
  file_type: string;
  file_size: number;
  created_at?: Date;
}

// Polling interval for real-time updates (5 seconds)
let pollingInterval: NodeJS.Timeout | null = null;
let currentUserId: number | null = null;

export const MessagingService = {
  // Start real-time polling for messages
  startPolling(userId: number, onUpdate?: (data: any) => void): void {
    // Stop any existing polling
    this.stopPolling();
    
    currentUserId = userId;
    
    // Start 5-second polling interval
    pollingInterval = setInterval(async () => {
      try {
        // Poll for new messages and unread counts
        const [conversations, unreadCount] = await Promise.all([
          this.getConversations(userId),
          this.getUnreadCount(userId)
        ]);
        
        // Call the update callback if provided
        if (onUpdate) {
          onUpdate({ conversations, unreadCount });
        }
      } catch (error) {
        console.error('Polling error:', error);
        // Don't stop polling on error, just log it
      }
    }, 5000); // 5 seconds
  },

  // Stop real-time polling
  stopPolling(): void {
    if (pollingInterval) {
      clearInterval(pollingInterval);
      pollingInterval = null;
      currentUserId = null;
    }
  },

  // Get conversations for a user
  async getConversations(userId: number): Promise<ConversationSummary[]> {
    try {
      const response = await fetch(`/api/messaging.php?user_id=${userId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting conversations:', error);
      throw new Error('Failed to get conversations');
    }
  },

  // Get a single conversation with messages
  async getConversation(conversationId: number): Promise<Conversation | null> {
    try {
      const response = await fetch(`/api/messaging.php?conversation_id=${conversationId}`);
      
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting conversation:', error);
      throw new Error('Failed to get conversation');
    }
  },

  // Create a new conversation
  async createConversation(conversation: Omit<Conversation, 'id' | 'created_at' | 'updated_at' | 'participants' | 'messages'>): Promise<Conversation> {
    try {
      const response = await fetch('/api/messaging.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(conversation),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const result = await response.json();
      return { ...conversation, id: result.id, created_at: new Date(), updated_at: new Date(), participants: [], messages: [] };
    } catch (error) {
      console.error('Error creating conversation:', error);
      throw new Error('Failed to create conversation');
    }
  },

  // Send a message
  async sendMessage(conversationId: number, senderId: number, content: string): Promise<Message> {
    try {
      const response = await fetch('/api/messaging.php?action=send_message', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          conversation_id: conversationId,
          sender_id: senderId,
          content,
          message_type: 'text'
        }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const result = await response.json();
      return {
        id: result.id,
        conversation_id: conversationId,
        sender_id: senderId,
        content,
        message_type: 'text',
        created_at: new Date(),
        updated_at: new Date()
      };
    } catch (error) {
      console.error('Error sending message:', error);
      throw new Error('Failed to send message');
    }
  },

  // Add participant to conversation
  async addParticipant(conversationId: number, userId: number): Promise<boolean> {
    try {
      const response = await fetch('/api/messaging.php?action=add_participant', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          conversation_id: conversationId,
          user_id: userId
        }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error adding participant:', error);
      throw new Error('Failed to add participant');
    }
  },

  // Remove participant from conversation
  async removeParticipant(conversationId: number, userId: number): Promise<boolean> {
    try {
      const response = await fetch('/api/messaging.php?action=remove_participant', {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          conversation_id: conversationId,
          user_id: userId
        }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error removing participant:', error);
      throw new Error('Failed to remove participant');
    }
  },

  // Mark messages as read
  async markMessagesAsRead(conversationId: number, userId: number): Promise<boolean> {
    try {
      const response = await fetch('/api/messaging.php?action=mark_read', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          conversation_id: conversationId,
          user_id: userId
        }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error marking messages as read:', error);
      throw new Error('Failed to mark messages as read');
    }
  },

  // Get unread message count for a user
  async getUnreadCount(userId: number): Promise<number> {
    try {
      const response = await fetch(`/api/messaging.php?action=unread_count&user_id=${userId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const result = await response.json();
      return result.unread_count || 0;
    } catch (error) {
      console.error('Error getting unread count:', error);
      throw new Error('Failed to get unread count');
    }
  },

  // Check if polling is currently active
  isPolling(): boolean {
    return pollingInterval !== null;
  },

  // Get the current user ID being polled
  getCurrentPollingUser(): number | null {
    return currentUserId;
  }
};
