import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import { CarService, Car } from '../services/carService';
import { PaymentService } from '../services/paymentService';
import { Star } from 'lucide-react'; // Added Star icon import

const Home: React.FC = () => {
  const [featuredCars, setFeaturedCars] = useState<Car[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadFeaturedCars();
  }, []);

  const loadFeaturedCars = async () => {
    try {
      setLoading(true);
      // Get actual featured cars from the featured_cars table
      const featuredCarsData = await PaymentService.getFeaturedCars();
      setFeaturedCars(featuredCarsData.slice(0, 3));
    } catch (error) {
      console.error('Error loading featured cars:', error);
      // Fallback to regular cars if featured cars fail to load
      try {
        const cars = await CarService.getCars();
        setFeaturedCars(cars.slice(0, 3));
      } catch (fallbackError) {
        console.error('Error loading fallback cars:', fallbackError);
        setFeaturedCars([]);
      }
    } finally {
      setLoading(false);
    }
  };



  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      {/* Hero Section */}
      <section className="relative bg-gradient-to-r from-primary-600 to-primary-700 text-white py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h1 className="text-4xl md:text-6xl font-bold mb-6">
            Find Your Perfect Ride
          </h1>
          <p className="text-xl md:text-2xl mb-8 text-primary-100 max-w-3xl mx-auto">
            The premier destination for car enthusiasts. Buy, sell, and discover amazing vehicles from trusted sellers and dealers.
          </p>
        </div>
      </section>

      

       {/* Featured Cars Section */}
       <section className="py-16 bg-white dark:bg-gray-800">
         <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
           <div className="flex justify-between items-center mb-8">
             <h2 className="text-3xl md:text-4xl font-bold text-gray-900 dark:text-white">Featured Cars</h2>
             <Link to="/browse" className="text-primary-600 hover:text-primary-700 dark:text-primary-400 dark:hover:text-primary-300 font-medium">
               View All Cars 
             </Link>
           </div>
           <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
             {loading ? (
               // Loading skeleton
               Array.from({ length: 3 }).map((_, index) => (
                 <div key={index} className="card animate-pulse">
                   <div className="h-48 bg-gray-200 dark:bg-gray-700 rounded-lg mb-4"></div>
                   <div className="space-y-2">
                     <div className="h-6 bg-gray-200 dark:bg-gray-700 rounded"></div>
                     <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded"></div>
                     <div className="h-4 bg-gray-200 dark:bg-gray-700 rounded"></div>
                   </div>
                 </div>
               ))
             ) : (
               featuredCars.map((car) => (
                 <div key={car.id} className="card hover:shadow-lg transition-shadow duration-300">
                   <div className="relative mb-4">
                     <img
                       src={car.images?.[0] || 'https://via.placeholder.com/400x300?text=No+Image'}
                       alt={`${car.year} ${car.make} ${car.model}`}
                       className="w-full h-48 object-cover rounded-lg"
                     />
                     {car.dealer_id && (
                       <span className="absolute top-2 left-2 bg-primary-600 text-white text-xs px-2 py-1 rounded-full">
                         Dealer
                       </span>
                     )}
                     {car.featured_status === 'active' && (
                       <span className="absolute top-2 right-2 bg-yellow-500 text-white text-xs px-2 py-1 rounded-full flex items-center">
                         <Star className="h-3 w-3 mr-1" /> Featured
                       </span>
                     )}
                   </div>
                   <div className="space-y-2">
                     <h3 className="text-xl font-semibold text-gray-900 dark:text-white">{car.year} {car.make} {car.model}</h3>
                     <div className="flex items-center justify-between">
                       <span className="text-2xl font-bold text-primary-600">${car.price.toLocaleString()}</span>
                       <span className="text-sm text-gray-500 dark:text-gray-400">{car.year}</span>
                     </div>
                     <div className="text-sm text-gray-600 dark:text-gray-300 space-y-1">
                       {car.mileage && (
                         <div className="flex justify-between">
                           <span>Mileage:</span>
                           <span>{car.mileage.toLocaleString()} miles</span>
                         </div>
                       )}

                       <div className="flex justify-between">
                         <span>Seller:</span>
                         <span className="text-primary-600 dark:text-primary-400 font-medium">
                           #{car.seller_id}
                         </span>
                       </div>
                     </div>
                     <Link
                       to={`/car/${car.id}`}
                       className="btn-primary w-full text-center block"
                     >
                       View Details
                     </Link>
                   </div>
                 </div>
               ))
             )}
           </div>
         </div>
       </section>

       


           </div>
  );
};

export default Home;
