import React, { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useUser } from '../contexts/UserContext';
import { gtaWorldAuthService } from '../services/gtaWorldAuthService';
import { GTAWorldCharacter } from '../config/gtaWorld';
import { CheckCircle, AlertCircle, Loader2, User } from 'lucide-react';

const AuthCallback: React.FC = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { login } = useUser();
  const [step, setStep] = useState<'loading' | 'characters' | 'error'>('loading');
  const [characters, setCharacters] = useState<GTAWorldCharacter[]>([]);
  const [selectedCharacter, setSelectedCharacter] = useState<GTAWorldCharacter | null>(null);
  const [error, setError] = useState<string>('');
  const [isAuthenticating, setIsAuthenticating] = useState(false);

  useEffect(() => {
    const code = searchParams.get('code');
    if (!code) {
      setError('No authorization code received');
      setStep('error');
      return;
    }

    handleOAuthCallback(code);
  }, [searchParams]);

  const handleOAuthCallback = async (code: string) => {
    try {
      // Exchange code for token
      const tokenResponse = await gtaWorldAuthService.exchangeCodeForToken(code);
      gtaWorldAuthService.storeToken(tokenResponse.access_token);

      // Get user info
      const userInfo = await gtaWorldAuthService.getUserInfo(tokenResponse.access_token);

      // Get user characters
      const userCharacters = await gtaWorldAuthService.getUserCharacters(tokenResponse.access_token);

      if (userCharacters.length === 0) {
        setError('No characters found for this account');
        setStep('error');
        return;
      }

      if (userCharacters.length === 1) {
        // Auto-select if only one character
        await authenticateWithCharacter(userCharacters[0]);
      } else {
        // Show character selection
        setCharacters(userCharacters);
        setStep('characters');
      }
    } catch (err) {
      console.error('OAuth callback error:', err);
      setError(err instanceof Error ? err.message : 'Authentication failed');
      setStep('error');
    }
  };

  const authenticateWithCharacter = async (character: GTAWorldCharacter) => {
    try {
      setIsAuthenticating(true);
      
      // Get user info again for authentication
      const token = gtaWorldAuthService.getStoredToken();
      if (!token) throw new Error('No token available');
      
      const userInfo = await gtaWorldAuthService.getUserInfo(token);
      
      // Authenticate with Car Spot system
      const authUser = await gtaWorldAuthService.authenticateUser(userInfo, character);
      
      // Login the user
      await login(authUser);
      
      // Check if user needs to complete profile
      if (!authUser.phone_number || !authUser.routing_number) {
        navigate('/complete-profile');
        return;
      }
      
      // Redirect to stored return URL or home page
      const returnUrl = gtaWorldAuthService.getAndClearReturnUrl();
      navigate(returnUrl || '/');
    } catch (err) {
      console.error('Authentication error:', err);
      setError(err instanceof Error ? err.message : 'Failed to authenticate');
      setStep('error');
    } finally {
      setIsAuthenticating(false);
    }
  };

  const handleCharacterSelect = async (character: GTAWorldCharacter) => {
    setSelectedCharacter(character);
    await authenticateWithCharacter(character);
  };

  if (step === 'loading') {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
        <div className="text-center">
          <Loader2 className="h-12 w-12 text-primary-600 animate-spin mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
            Authenticating with GTA World
          </h2>
          <p className="text-gray-600 dark:text-gray-400">
            Please wait while we verify your account...
          </p>
        </div>
      </div>
    );
  }

  if (step === 'error') {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
        <div className="text-center max-w-md mx-auto px-4">
          <AlertCircle className="h-12 w-12 text-red-600 mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
            Authentication Failed
          </h2>
          <p className="text-gray-600 dark:text-gray-400 mb-6">
            {error}
          </p>
          <button
            onClick={() => navigate('/')}
            className="btn-primary"
          >
            Return to Home
          </button>
        </div>
      </div>
    );
  }

  if (step === 'characters') {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-12 px-4">
        <div className="max-w-4xl mx-auto">
          <div className="text-center mb-8">
            <CheckCircle className="h-12 w-12 text-green-600 mx-auto mb-4" />
            <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">
              Select Your Character
            </h1>
            <p className="text-gray-600 dark:text-gray-400">
              Choose which character you want to use with Car Spot
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {characters.map((character) => (
              <div
                key={character.id}
                className={`bg-white dark:bg-gray-800 rounded-lg shadow-md p-6 cursor-pointer transition-all duration-200 hover:shadow-lg hover:scale-105 border-2 ${
                  selectedCharacter?.id === character.id
                    ? 'border-primary-500 ring-2 ring-primary-200'
                    : 'border-gray-200 dark:border-gray-700'
                }`}
                onClick={() => handleCharacterSelect(character)}
              >
                <div className="text-center">
                  <div className="w-20 h-20 mx-auto mb-4 rounded-full bg-gradient-to-br from-primary-400 to-primary-600 flex items-center justify-center">
                    {character.avatar_url ? (
                      <img
                        src={character.avatar_url}
                        alt={character.name}
                        className="w-16 h-16 rounded-full object-cover"
                      />
                    ) : (
                      <User className="h-8 w-8 text-white" />
                    )}
                  </div>
                  
                  <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
                    {character.name}
                  </h3>
                  
                  {/* Only show real data that GTA World provides */}
                  <div className="text-sm text-gray-500 dark:text-gray-400">
                    Character ID: {character.id}
                  </div>
                </div>
              </div>
            ))}
          </div>

          {isAuthenticating && (
            <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
              <div className="bg-white dark:bg-gray-800 rounded-lg p-6 text-center">
                <Loader2 className="h-8 w-8 text-primary-600 animate-spin mx-auto mb-4" />
                <p className="text-gray-900 dark:text-white">
                  Setting up your account...
                </p>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }

  return null;
};

export default AuthCallback;
