// API configuration
export const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'https://carspot.site';

// Helper function to build API URLs
export const buildApiUrl = (endpoint: string): string => {
  return `${API_BASE_URL}/api/${endpoint}`;
};

// Default fetch options
export const defaultFetchOptions = {
  headers: {
    'Content-Type': 'application/json',
  },
};

// API endpoints for admin functionality
export const ADMIN_ENDPOINTS = {
  SETTINGS: 'payments.php?action=admin_settings',
  UPDATE_SETTING: 'payments.php?action=update_setting',
  USERS: 'users.php',
  DEALERS: 'dealers.php',
  REPORTS: 'reports.php',
  ANALYTICS: 'analytics.php',
  PAYMENTS: 'payments.php',
  CARS: 'cars.php',
};

// Debug function to help troubleshoot API calls
export const debugApiCall = (endpoint: string, fullUrl: string) => {
  console.log(`API Call Debug:`);
  console.log(`- Endpoint: ${endpoint}`);
  console.log(`- Full URL: ${fullUrl}`);
  console.log(`- Base URL: ${API_BASE_URL}`);
};


