import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { PaymentService, PaymentPlan, DealerMembership, PaymentTransaction, UserSubscription, FeaturedCar } from '../services/paymentService';
import { useUser } from './UserContext';

export interface PaymentState {
  paymentPlans: PaymentPlan[];
  userSubscriptions: UserSubscription[];
  dealerMembership: DealerMembership | null;
  paymentHistory: PaymentTransaction[];
  featuredCars: FeaturedCar[];
  isLoading: boolean;
  error: string | null;
}

interface PaymentContextType extends PaymentState {
  refreshPaymentData: () => Promise<void>;
  refreshUserSubscriptions: () => Promise<void>;
  refreshDealerMembership: () => Promise<void>;
  refreshPaymentHistory: () => Promise<void>;
  refreshFeaturedCars: () => Promise<void>;
  clearError: () => void;
}

const PaymentContext = createContext<PaymentContextType | undefined>(undefined);

export const usePayment = () => {
  const context = useContext(PaymentContext);
  if (context === undefined) {
    throw new Error('usePayment must be used within a PaymentProvider');
  }
  return context;
};

interface PaymentProviderProps {
  children: ReactNode;
}

export const PaymentProvider: React.FC<PaymentProviderProps> = ({ children }) => {
  const { user } = useUser();
  const [state, setState] = useState<PaymentState>({
    paymentPlans: [],
    userSubscriptions: [],
    dealerMembership: null,
    paymentHistory: [],
    featuredCars: [],
    isLoading: false,
    error: null
  });

  // Load initial payment data
  useEffect(() => {
    if (user) {
      refreshPaymentData();
    }
  }, [user]);

  const refreshPaymentData = async () => {
    if (!user) return;
    
    setState(prev => ({ ...prev, isLoading: true, error: null }));
    
    try {
      const [
        plans,
        subscriptions,
        paymentHistory,
        featuredCars
      ] = await Promise.all([
        PaymentService.getPaymentPlans(),
        PaymentService.getUserSubscriptions(user.id),
        PaymentService.getUserPaymentHistory(user.id),
        PaymentService.getFeaturedCars()
      ]);

      // Get dealer membership if user is a dealer
      let dealerMembership: DealerMembership | null = null;
      if (user.is_dealer) {
        try {
          // Note: This would need the dealer_account_id from the user context
          // For now, we'll handle this in the dealer portal
          dealerMembership = null;
        } catch (error) {
          console.log('User is not associated with a dealer account');
        }
      }

      setState(prev => ({
        ...prev,
        paymentPlans: plans,
        userSubscriptions: subscriptions,
        dealerMembership,
        paymentHistory,
        featuredCars,
        isLoading: false
      }));
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : 'Failed to load payment data',
        isLoading: false
      }));
    }
  };

  const refreshUserSubscriptions = async () => {
    if (!user) return;
    
    try {
      const subscriptions = await PaymentService.getUserSubscriptions(user.id);
      setState(prev => ({ ...prev, userSubscriptions: subscriptions }));
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : 'Failed to refresh subscriptions'
      }));
    }
  };

  const refreshDealerMembership = async (dealerAccountId?: number) => {
    if (!user || !dealerAccountId) return;
    
    try {
      const membership = await PaymentService.getDealerMembership(dealerAccountId);
      setState(prev => ({ ...prev, dealerMembership: membership }));
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : 'Failed to refresh dealer membership'
      }));
    }
  };

  const refreshPaymentHistory = async () => {
    if (!user) return;
    
    try {
      const history = await PaymentService.getUserPaymentHistory(user.id);
      setState(prev => ({ ...prev, paymentHistory: history }));
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : 'Failed to refresh payment history'
      }));
    }
  };

  const refreshFeaturedCars = async () => {
    try {
      const featured = await PaymentService.getFeaturedCars();
      setState(prev => ({ ...prev, featuredCars: featured }));
    } catch (error) {
      setState(prev => ({
        ...prev,
        error: error instanceof Error ? error.message : 'Failed to refresh featured cars'
      }));
    }
  };

  const clearError = () => {
    setState(prev => ({ ...prev, error: null }));
  };

  const value: PaymentContextType = {
    ...state,
    refreshPaymentData,
    refreshUserSubscriptions,
    refreshDealerMembership,
    refreshPaymentHistory,
    refreshFeaturedCars,
    clearError
  };

  return (
    <PaymentContext.Provider value={value}>
      {children}
    </PaymentContext.Provider>
  );
};
