import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { DarkModeProvider } from './contexts/DarkModeContext';
import { UserProvider } from './contexts/UserContext';
import { PaymentProvider } from './contexts/PaymentContext';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import FloatingMessages from './components/FloatingMessages';
import Home from './pages/Home';
import BrowseCars from './pages/BrowseCars';
import SellCar from './pages/SellCar';
import DealerPortal from './pages/DealerPortal';
import CarDetails from './pages/CarDetails';
import DealerApplication from './pages/DealerApplication';
import DealerApplicationsAdmin from './pages/DealerApplicationsAdmin';
import UserProfile from './pages/UserProfile';
import StaffPortal from './pages/StaffPortal';
import PaymentHistory from './components/PaymentHistory';
import AuthCallback from './pages/AuthCallback';
import Login from './pages/Login';
import Support from './pages/Support';
import './App.css';
import CompleteProfile from './pages/CompleteProfile';
import ProtectedRoute from './components/ProtectedRoute';
import ProfileCompletionBanner from './components/ProfileCompletionBanner';
import Profile from './pages/Profile';

function App() {
  return (
    <DarkModeProvider>
      <UserProvider>
        <PaymentProvider>
          <Router>
            <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
              <Navbar />
              <ProfileCompletionBanner />
              <main className="flex-1">
                <Routes>
                  <Route path="/" element={<Home />} />
                  <Route path="/browse" element={<BrowseCars />} />
                  <Route path="/sell" element={
                    <ProtectedRoute>
                      <SellCar />
                    </ProtectedRoute>
                  } />
                  <Route path="/dealer" element={
                    <ProtectedRoute>
                      <DealerPortal />
                    </ProtectedRoute>
                  } />
                  <Route path="/car/:id" element={<CarDetails />} />
                  <Route path="/become-dealer" element={
                    <ProtectedRoute>
                      <DealerApplication />
                    </ProtectedRoute>
                  } />
                  <Route path="/user/:userId" element={<UserProfile />} />
                  <Route path="/staff" element={
                    <ProtectedRoute>
                      <StaffPortal />
                    </ProtectedRoute>
                  } />
                  <Route path="/admin/dealer-applications" element={
                    <ProtectedRoute>
                      <DealerApplicationsAdmin />
                    </ProtectedRoute>
                  } />
                  <Route path="/payment-history" element={
                    <ProtectedRoute>
                      <PaymentHistory />
                    </ProtectedRoute>
                  } />
                  <Route path="/support" element={
                    <ProtectedRoute>
                      <Support />
                    </ProtectedRoute>
                  } />
                  <Route path="/complete-profile" element={<CompleteProfile />} />
                  <Route path="/profile" element={<Profile />} />
                  <Route path="/auth/callback" element={<AuthCallback />} />
                  <Route path="/login" element={<Login />} />
                </Routes>
              </main>
              <Footer />
              <FloatingMessages />
            </div>
          </Router>
        </PaymentProvider>
      </UserProvider>
    </DarkModeProvider>
  );
}

export default App;
