import React, { useState, useEffect, useCallback } from 'react';
import { MessageSquare, X, Send, User, Building2 } from 'lucide-react';
import { useUser } from '../contexts/UserContext';
import { MessagingService, ConversationSummary } from '../services/messagingService';

const FloatingMessages: React.FC = () => {
  const { user } = useUser();
  const [isOpen, setIsOpen] = useState(false);
  const [conversations, setConversations] = useState<ConversationSummary[]>([]);
  const [selectedConversation, setSelectedConversation] = useState<ConversationSummary | null>(null);
  const [messages, setMessages] = useState<any[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [sending, setSending] = useState(false);

  const loadMessages = useCallback(async (conversationId: number) => {
    try {
      const conversation = await MessagingService.getConversation(conversationId);
      if (conversation && conversation.messages) {
        setMessages(conversation.messages);
        // Mark messages as read
        await MessagingService.markMessagesAsRead(conversationId, user!.id);
      }
    } catch (error) {
      console.error('Error loading messages:', error);
    }
  }, [user]);

  const loadConversations = useCallback(async () => {
    if (!user) return;
    
    try {
      setLoading(true);
      const conversationsData = await MessagingService.getConversations(user.id);
      setConversations(conversationsData);
      if (conversationsData.length > 0) {
        setSelectedConversation(conversationsData[0]);
        loadMessages(conversationsData[0].id);
      }
    } catch (error) {
      console.error('Error loading conversations:', error);
    } finally {
      setLoading(false);
    }
  }, [user, loadMessages]);

  const sendMessage = async () => {
    if (!newMessage.trim() || !selectedConversation?.id || !user || sending) return;

    try {
      setSending(true);
      const message = await MessagingService.sendMessage(
        selectedConversation.id,
        user.id,
        newMessage.trim()
      );
      
      setMessages(prev => [...prev, message]);
      setNewMessage('');
      
      // Refresh conversations to update last message
      await loadConversations();
    } catch (error) {
      console.error('Error sending message:', error);
    } finally {
      setSending(false);
    }
  };

  const handleConversationSelect = (conversation: ConversationSummary) => {
    setSelectedConversation(conversation);
    loadMessages(conversation.id);
  };

  const toggleOpen = () => {
    setIsOpen(!isOpen);
  };

  // Calculate total unread count
  const totalUnreadCount = conversations.reduce((total, conv) => total + conv.unread_count, 0);

  // Load conversations when user is available
  useEffect(() => {
    if (user && isOpen) {
      loadConversations();
    }
  }, [user, isOpen, loadConversations]);

  // Don't show if no user is logged in
  if (!user) {
    return null;
  }

  return (
    <div className="fixed bottom-6 right-6 z-50">
      {/* Floating Message Button */}
      <button
        onClick={toggleOpen}
        className="relative bg-primary-600 hover:bg-primary-700 text-white rounded-full p-4 shadow-lg hover:shadow-xl transition-all duration-200 transform hover:scale-105"
      >
        <MessageSquare className="h-6 w-6" />
        {/* Notification Badge */}
        {totalUnreadCount > 0 && (
          <span className="absolute -top-2 -right-2 bg-red-500 text-white text-xs rounded-full h-6 w-6 flex items-center justify-center font-medium animate-pulse">
            {totalUnreadCount > 9 ? '9+' : totalUnreadCount}
          </span>
        )}
      </button>

      {/* Messages Panel */}
      {isOpen && (
        <div className="absolute bottom-16 right-0 w-80 bg-white dark:bg-gray-800 rounded-lg shadow-2xl border border-gray-200 dark:border-gray-700">
          {/* Header */}
          <div className="flex items-center justify-between p-4 border-b border-gray-200 dark:border-gray-700">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white">Messages</h3>
            <button
              onClick={toggleOpen}
              className="text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
            >
              <X className="h-5 w-5" />
            </button>
          </div>

          {/* Conversations List */}
          <div className="max-h-64 overflow-y-auto border-b border-gray-200 dark:border-gray-700">
            {loading ? (
              <div className="p-4 text-center text-gray-500 dark:text-gray-400">
                Loading conversations...
              </div>
            ) : conversations.length === 0 ? (
              <div className="p-4 text-center text-gray-500 dark:text-gray-400">
                No conversations yet
              </div>
            ) : (
              conversations.map((conversation) => (
                <div
                  key={conversation.id}
                  onClick={() => handleConversationSelect(conversation)}
                  className={`p-3 border-b border-gray-100 dark:border-gray-700 cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors duration-150 ${
                    selectedConversation?.id === conversation.id ? 'bg-primary-50 dark:bg-primary-900/20' : ''
                  }`}
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center space-x-2">
                        {conversation.conversation_type === 'direct' ? (
                          <User className="h-4 w-4 text-gray-500" />
                        ) : (
                          <Building2 className="h-4 w-4 text-gray-500" />
                        )}
                        <p className="text-sm font-medium text-gray-900 dark:text-white truncate">
                          {conversation.title || 'Direct Message'}
                        </p>
                      </div>
                      <p className="text-sm text-gray-600 dark:text-gray-300 truncate mt-1">
                        {conversation.last_message.content}
                      </p>
                      <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                        {formatTime(conversation.last_message.created_at)}
                      </p>
                    </div>
                    {conversation.unread_count > 0 && (
                      <div className="ml-2 flex items-center space-x-1">
                        <span className="text-xs bg-blue-500 text-white rounded-full px-2 py-1 min-w-[20px] text-center">
                          {conversation.unread_count}
                        </span>
                      </div>
                    )}
                  </div>
                </div>
              ))
            )}
          </div>

          {/* Messages List */}
          {selectedConversation && (
            <div className="max-h-64 overflow-y-auto">
              {messages.length === 0 ? (
                <div className="p-4 text-center text-gray-500 dark:text-gray-400">
                  No messages in this conversation
                </div>
              ) : (
                messages.map((message) => (
                  <div
                    key={message.id}
                    className={`p-3 border-b border-gray-100 dark:border-gray-700 ${
                      message.sender_id === user?.id ? 'bg-blue-50 dark:bg-blue-900/20' : ''
                    }`}
                  >
                    <div className="flex items-start justify-between">
                      <div className="flex-1 min-w-0">
                        <p className="text-xs text-gray-500 dark:text-gray-400 mb-1">
                          {message.sender_id === user?.id ? 'You' : 'Other'}
                        </p>
                        <p className="text-sm text-gray-900 dark:text-white">
                          {message.content}
                        </p>
                        <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                          {formatTime(message.created_at)}
                        </p>
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          )}

          {/* Quick Reply */}
          {selectedConversation && (
            <div className="p-4 border-t border-gray-200 dark:border-gray-700">
              <div className="flex items-center space-x-2">
                <input
                  type="text"
                  placeholder="Type a message..."
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
                  className="flex-1 px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100 placeholder-gray-500 dark:placeholder-gray-400 text-sm"
                />
                <button 
                  onClick={sendMessage}
                  disabled={sending || !newMessage.trim()}
                  className="p-2 bg-primary-600 hover:bg-primary-700 disabled:bg-gray-400 text-white rounded-lg transition-colors duration-150 disabled:cursor-not-allowed"
                >
                  <Send className="h-4 w-4" />
                </button>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

// Helper function to format time
const formatTime = (dateString: string | Date): string => {
  if (!dateString) return '';
  
  const date = new Date(dateString);
  const now = new Date();
  const diffInMinutes = Math.floor((now.getTime() - date.getTime()) / (1000 * 60));
  
  if (diffInMinutes < 1) return 'Just now';
  if (diffInMinutes < 60) return `${diffInMinutes}m ago`;
  if (diffInMinutes < 1440) return `${Math.floor(diffInMinutes / 60)}h ago`;
  if (diffInMinutes < 10080) return `${Math.floor(diffInMinutes / 1440)}d ago`;
  
  return date.toLocaleDateString();
};

export default FloatingMessages;
