import React, { useState, useEffect } from 'react';
import { useUser } from '../contexts/UserContext';
import { TicketService, SupportTicket, TicketCategory } from '../services/ticketService';
import { triggerNewTicket } from '../services/webhookService';
import { 
  Plus, 
  MessageSquare, 
  Clock, 
  CheckCircle, 
  XCircle, 
  AlertCircle,
  Filter,
  Search,
  Star,
  FileText,
  User,
  Calendar,
  Tag
} from 'lucide-react';

const Support: React.FC = () => {
  const { user } = useUser();
  const [activeTab, setActiveTab] = useState<'create' | 'my-tickets'>('create');
  const [tickets, setTickets] = useState<SupportTicket[]>([]);
  const [categories, setCategories] = useState<TicketCategory[]>([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [filters, setFilters] = useState({
    status: '',
    category: '',
    priority: '',
  });

  // Form state for creating tickets
  const [ticketForm, setTicketForm] = useState<{
    subject: string;
    description: string;
    category_id: string;
    priority: 'low' | 'medium' | 'high' | 'urgent';
  }>({
    subject: '',
    description: '',
    category_id: '',
    priority: 'medium',
  });

  useEffect(() => {
    if (user) {
      loadCategories();
      if (activeTab === 'my-tickets') {
        loadUserTickets();
      }
    }
  }, [user, activeTab, currentPage, filters]);

  const loadCategories = async () => {
    try {
      console.log('Loading categories...');
      const categoriesData = await TicketService.getCategories();
      console.log('Categories loaded:', categoriesData);
      setCategories(categoriesData);
    } catch (error) {
      console.error('Error loading categories:', error);
    }
  };

  // Tags system removed - using only categories

  const loadUserTickets = async () => {
    if (!user) return;
    
    try {
      setLoading(true);
      const ticketFilters = {
        status: filters.status || undefined,
        category: filters.category ? parseInt(filters.category) : undefined,
      };
      
      const result = await TicketService.getUserTickets(user.id, ticketFilters, currentPage);
      setTickets(result.tickets);
      setTotalPages(result.pagination.pages);
    } catch (error) {
      console.error('Error loading tickets:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleCreateTicket = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!user) return;

    try {
      setLoading(true);
      const newTicket = await TicketService.createTicket({
        user_id: user.id,
        category_id: parseInt(ticketForm.category_id),
        subject: ticketForm.subject,
        description: ticketForm.description,
        priority: ticketForm.priority,
      });

      // Reset form
      setTicketForm({
        subject: '',
        description: '',
        category_id: '',
        priority: 'medium',
      });

      // Switch to my tickets tab and show the new ticket
      setActiveTab('my-tickets');
      setTickets(prev => [newTicket, ...prev]);
      
      // Trigger webhook for new ticket
      await triggerNewTicket({
        username: user.name || 'Unknown User',
        subject: ticketForm.subject,
        ticket_url: `https://carspot.site/support/tickets/${newTicket.id}`
      });
      
      // Show success message
      alert('Ticket created successfully!');
    } catch (error) {
      console.error('Error creating ticket:', error);
      alert('Failed to create ticket. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleFilterChange = (key: string, value: string) => {
    setFilters(prev => ({ ...prev, [key]: value }));
    setCurrentPage(1); // Reset to first page when filters change
  };

  const clearFilters = () => {
    setFilters({
      status: '',
      category: '',
      priority: '',
    });
    setCurrentPage(1);
  };



  if (!user) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-4">
              Support Center
            </h1>
            <p className="text-gray-600 dark:text-gray-400 mb-8">
              Please log in to access the support center.
            </p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">
            Support Center
          </h1>
          <p className="text-lg text-gray-600 dark:text-gray-400">
            Get help with your Car Spot experience
          </p>
        </div>

        {/* Navigation Tabs */}
        <div className="flex space-x-1 bg-white dark:bg-gray-800 p-1 rounded-lg shadow-sm mb-8">
          <button
            onClick={() => setActiveTab('create')}
            className={`flex-1 py-3 px-4 rounded-md text-sm font-medium transition-colors ${
              activeTab === 'create'
                ? 'bg-primary-600 text-white shadow-sm'
                : 'text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white'
            }`}
          >
            <Plus className="h-4 w-4 inline mr-2" />
            Create Ticket
          </button>
          <button
            onClick={() => setActiveTab('my-tickets')}
            className={`flex-1 py-3 px-4 rounded-md text-sm font-medium transition-colors ${
              activeTab === 'my-tickets'
                ? 'bg-primary-600 text-white shadow-sm'
                : 'text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white'
            }`}
          >
            <MessageSquare className="h-4 w-4 inline mr-2" />
            My Tickets
          </button>
        </div>

        {/* Content */}
        {activeTab === 'create' ? (
          <CreateTicketForm
            form={ticketForm}
            setForm={setTicketForm}
            categories={categories}
            onSubmit={handleCreateTicket}
            loading={loading}
          />
        ) : (
          <MyTickets
            tickets={tickets}
            categories={categories}
            filters={filters}
            onFilterChange={handleFilterChange}
            onClearFilters={clearFilters}
            loading={loading}
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={setCurrentPage}
          />
        )}
      </div>
    </div>
  );
};

// Create Ticket Form Component
interface CreateTicketFormProps {
  form: {
    subject: string;
    description: string;
    category_id: string;
    priority: 'low' | 'medium' | 'high' | 'urgent';
  };
  setForm: React.Dispatch<React.SetStateAction<{
    subject: string;
    description: string;
    category_id: string;
    priority: 'low' | 'medium' | 'high' | 'urgent';
  }>>;
  categories: TicketCategory[];
  onSubmit: (e: React.FormEvent) => void;
  loading: boolean;
}

const CreateTicketForm: React.FC<CreateTicketFormProps> = ({
  form,
  setForm,
  categories,
  onSubmit,
  loading,
}) => {
  return (
    <div className="max-w-2xl mx-auto">
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-6">
          Create Support Ticket
        </h2>

        <form onSubmit={onSubmit} className="space-y-6">
          {/* Subject */}
          <div>
            <label htmlFor="subject" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Subject *
            </label>
            <input
              type="text"
              id="subject"
              required
              value={form.subject}
              onChange={(e) => setForm(prev => ({ ...prev, subject: e.target.value }))}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
              placeholder="Brief description of your issue"
            />
          </div>

          {/* Category */}
          <div>
            <label htmlFor="category" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Category *
            </label>
            <select
              id="category"
              required
              value={form.category_id}
              onChange={(e) => setForm(prev => ({ ...prev, category_id: e.target.value }))}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            >
              <option value="">Select a category</option>
              {categories.map((category) => (
                <option key={category.id} value={category.id}>
                  {category.name}
                </option>
              ))}
            </select>
          </div>

          {/* Priority */}
          <div>
            <label htmlFor="priority" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Priority
            </label>
            <select
              id="priority"
              value={form.priority}
              onChange={(e) => setForm(prev => ({ ...prev, priority: e.target.value as any }))}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            >
              <option value="low">Low</option>
              <option value="medium">Medium</option>
              <option value="high">High</option>
              <option value="urgent">Urgent</option>
            </select>
          </div>

          {/* Description */}
          <div>
            <label htmlFor="description" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Description *
            </label>
            <textarea
              id="description"
              required
              rows={6}
              value={form.description}
              onChange={(e) => setForm(prev => ({ ...prev, description: e.target.value }))}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
              placeholder="Please provide detailed information about your issue..."
            />
          </div>

          {/* Submit Button */}
          <button
            type="submit"
            disabled={loading}
            className="w-full bg-primary-600 hover:bg-primary-700 disabled:bg-primary-400 text-white font-medium py-3 px-4 rounded-md transition-colors focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2"
          >
            {loading ? 'Creating Ticket...' : 'Create Ticket'}
          </button>
        </form>

        {/* Help Text */}
        <div className="mt-6 p-4 bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-md">
          <h3 className="text-sm font-medium text-blue-800 dark:text-blue-200 mb-2">
            Tips for faster support:
          </h3>
          <ul className="text-sm text-blue-700 dark:text-blue-300 space-y-1">
            <li> Be specific about your issue</li>
            <li> Include any error messages you see</li>
            <li> Mention what you were trying to do</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

// My Tickets Component
interface MyTicketsProps {
  tickets: SupportTicket[];
  categories: TicketCategory[];
  filters: {
    status: string;
    category: string;
    priority: string;
  };
  onFilterChange: (key: string, value: string) => void;
  onClearFilters: () => void;
  loading: boolean;
  currentPage: number;
  totalPages: number;
  onPageChange: (page: number) => void;
}

const MyTickets: React.FC<MyTicketsProps> = ({
  tickets,
  categories,
  filters,
  onFilterChange,
  onClearFilters,
  loading,
  currentPage,
  totalPages,
  onPageChange,
}) => {
  return (
    <div className="space-y-6">
      {/* Filters */}
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-medium text-gray-900 dark:text-white">
            <Filter className="h-5 w-5 inline mr-2" />
            Filters
          </h3>
          <button
            onClick={onClearFilters}
            className="text-sm text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
          >
            Clear All
          </button>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {/* Status Filter */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Status
            </label>
            <select
              value={filters.status}
              onChange={(e) => onFilterChange('status', e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            >
              <option value="">All Statuses</option>
              <option value="open">Open</option>
              <option value="in_progress">In Progress</option>
              <option value="waiting_for_user">Waiting for User</option>
              <option value="resolved">Resolved</option>
              <option value="closed">Closed</option>
            </select>
          </div>

          {/* Category Filter */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Category
            </label>
            <select
              value={filters.category}
              onChange={(e) => onFilterChange('category', e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            >
              <option value="">All Categories</option>
              {categories.map((category) => (
                <option key={category.id} value={category.id}>
                  {category.name}
                </option>
              ))}
            </select>
          </div>

          {/* Priority Filter */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Priority
            </label>
            <select
              value={filters.priority}
              onChange={(e) => onFilterChange('priority', e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 dark:focus:ring-primary-600 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100"
            >
              <option value="">All Priorities</option>
              <option value="low">Low</option>
              <option value="medium">Medium</option>
              <option value="high">High</option>
              <option value="urgent">Urgent</option>
            </select>
          </div>
        </div>
      </div>

      {/* Tickets List */}
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700">
        <div className="px-6 py-4 border-b border-gray-200 dark:border-gray-700">
          <h3 className="text-lg font-medium text-gray-900 dark:text-white">
            My Support Tickets ({tickets.length})
          </h3>
        </div>

        {loading ? (
          <div className="p-6 text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600 mx-auto"></div>
            <p className="mt-2 text-gray-600 dark:text-gray-400">Loading tickets...</p>
          </div>
        ) : tickets.length === 0 ? (
          <div className="p-6 text-center">
            <MessageSquare className="h-12 w-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-600 dark:text-gray-400">
              No tickets found. Create your first support ticket to get started.
            </p>
          </div>
        ) : (
          <div className="divide-y divide-gray-200 dark:divide-gray-700">
            {tickets.map((ticket) => (
              <TicketCard key={ticket.id} ticket={ticket} />
            ))}
          </div>
        )}

        {/* Pagination */}
        {totalPages > 1 && (
          <div className="px-6 py-4 border-t border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-between">
              <p className="text-sm text-gray-700 dark:text-gray-300">
                Page {currentPage} of {totalPages}
              </p>
              <div className="flex space-x-2">
                <button
                  onClick={() => onPageChange(currentPage - 1)}
                  disabled={currentPage === 1}
                  className="px-3 py-2 text-sm font-medium text-gray-500 bg-white border border-gray-300 rounded-md hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed dark:bg-gray-700 dark:border-gray-600 dark:text-gray-400 dark:hover:bg-gray-600"
                >
                  Previous
                </button>
                <button
                  onClick={() => onPageChange(currentPage + 1)}
                  disabled={currentPage === totalPages}
                  className="px-3 py-2 text-sm font-medium text-gray-500 bg-white border border-gray-300 rounded-md hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed dark:bg-gray-700 dark:border-gray-600 dark:text-gray-400 dark:hover:bg-gray-600"
                >
                  Next
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

// Helper function for status icons
const getStatusIcon = (status: SupportTicket['status']) => {
  switch (status) {
    case 'open':
      return <AlertCircle className="h-4 w-4 text-blue-500" />;
    case 'in_progress':
      return <Clock className="h-4 w-4 text-yellow-500" />;
    case 'waiting_for_user':
      return <User className="h-4 w-4 text-purple-500" />;
    case 'resolved':
      return <CheckCircle className="h-4 w-4 text-green-500" />;
    case 'closed':
      return <XCircle className="h-4 w-4 text-gray-500" />;
    default:
      return <FileText className="h-4 w-4 text-gray-500" />;
  }
};

// Ticket Card Component
interface TicketCardProps {
  ticket: SupportTicket;
}

const TicketCard: React.FC<TicketCardProps> = ({ ticket }) => {
  return (
    <div className="p-6 hover:bg-gray-50 dark:hover:bg-gray-700/50 transition-colors">
      <div className="flex items-start justify-between">
        <div className="flex-1">
          <div className="flex items-center space-x-3 mb-2">
            {getStatusIcon(ticket.status)}
            <h4 className="text-lg font-medium text-gray-900 dark:text-white">
              {ticket.subject}
            </h4>
          </div>
          
          <div className="flex items-center space-x-4 text-sm text-gray-600 dark:text-gray-400 mb-3">
            <span className="flex items-center">
              <Tag className="h-4 w-4 mr-1" />
              {ticket.category_name}
            </span>
            <span className={`px-2 py-1 rounded-full text-xs font-medium ${TicketService.getPriorityColor(ticket.priority)}`}>
              {TicketService.getPriorityLabel(ticket.priority)}
            </span>
            <span className={`px-2 py-1 rounded-full text-xs font-medium ${TicketService.getStatusColor(ticket.status)}`}>
              {TicketService.getStatusLabel(ticket.status)}
            </span>
          </div>

          <p className="text-gray-600 dark:text-gray-400 mb-3 line-clamp-2">
            {ticket.description}
          </p>

          <div className="flex items-center space-x-4 text-sm text-gray-500 dark:text-gray-400">
            <span className="flex items-center">
              <Calendar className="h-4 w-4 mr-1" />
              {TicketService.formatDate(ticket.created_at)}
            </span>
            <span className="flex items-center">
              <MessageSquare className="h-4 w-4 mr-1" />
              {ticket.response_count} response{ticket.response_count !== 1 ? 's' : ''}
            </span>
            {ticket.last_response && (
              <span className="flex items-center">
                <Clock className="h-4 w-4 mr-1" />
                Last response {TicketService.getTimeAgo(ticket.last_response)}
              </span>
            )}
          </div>
        </div>

        <div className="ml-4">
          <span className="text-sm font-mono text-gray-500 dark:text-gray-400">
            {TicketService.formatTicketNumber(ticket.ticket_number)}
          </span>
        </div>
      </div>

      {/* Tags system removed - using only categories */}
    </div>
  );
};

export default Support;
