import { GTA_WORLD_CONFIG, GTAWorldUser, GTAWorldCharacter, GTAWorldAuthResponse } from '../config/gtaWorld';

export interface AuthUser {
  id: number;
  name: string;
  email?: string;
  discord?: string | null;
  phone_number?: string;
  routing_number?: string;
  avatar_url?: string;
  is_dealer: boolean;

  staff_role?: 'admin' | 'moderator' | 'support' | 'analyst';
  company_name?: string;
  gta_world_id: number;
  gta_world_username: string;
  created_at?: string;
}

class GTAWorldAuthService {
  // Initiate OAuth flow with optional return URL
  initiateLogin(returnUrl?: string): void {
    // Store the return URL in sessionStorage if provided
    if (returnUrl) {
      sessionStorage.setItem('gta_world_return_url', returnUrl);
    }

    const params = new URLSearchParams({
      client_id: GTA_WORLD_CONFIG.CLIENT_ID,
      redirect_uri: GTA_WORLD_CONFIG.REDIRECT_URI,
      response_type: 'code',
      scope: 'identity'
    });

    const authUrl = `${GTA_WORLD_CONFIG.AUTH_URL}?${params.toString()}`;
    window.location.href = authUrl;
  }

  // Exchange authorization code for access token
  async exchangeCodeForToken(code: string): Promise<GTAWorldAuthResponse> {
    const response = await fetch('/api/auth/gta-world-token', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ code }),
    });

    if (!response.ok) {
      throw new Error('Failed to exchange code for token');
    }

    return response.json();
  }

  // Get user information from GTA World
  async getUserInfo(accessToken: string): Promise<GTAWorldUser> {
    const response = await fetch('/api/auth/gta-world-user', {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${accessToken}`,
      },
    });

    if (!response.ok) {
      throw new Error('Failed to fetch user info');
    }

    return response.json();
  }

  // Get user's characters from GTA World
  async getUserCharacters(accessToken: string): Promise<GTAWorldCharacter[]> {
    const response = await fetch('/api/auth/gta-world-characters', {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${accessToken}`,
      },
    });

    if (!response.ok) {
      throw new Error('Failed to fetch characters');
    }

    return response.json();
  }

  // Authenticate user with Car Spot system
  async authenticateUser(gtaWorldUser: GTAWorldUser, selectedCharacter?: GTAWorldCharacter): Promise<AuthUser> {
    const response = await fetch('/api/auth/authenticate', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        gta_world_user: gtaWorldUser,
        selected_character: selectedCharacter
      }),
    });

    if (!response.ok) {
      throw new Error('Authentication failed');
    }

    return response.json();
  }

  // Check if user is authenticated
  isAuthenticated(): boolean {
    return localStorage.getItem('gta_world_token') !== null;
  }

  // Get stored token
  getStoredToken(): string | null {
    return localStorage.getItem('gta_world_token');
  }

  // Store token
  storeToken(token: string): void {
    localStorage.setItem('gta_world_token', token);
  }

  // Clear stored token
  clearToken(): void {
    localStorage.removeItem('gta_world_token');
  }

  // Logout
  logout(): void {
    this.clearToken();
    localStorage.removeItem('user');
    localStorage.removeItem('selected_character');
  }

  // Get stored return URL and clear it
  getAndClearReturnUrl(): string | null {
    const returnUrl = sessionStorage.getItem('gta_world_return_url');
    sessionStorage.removeItem('gta_world_return_url');
    return returnUrl;
  }
}

export const gtaWorldAuthService = new GTAWorldAuthService();
export default gtaWorldAuthService;
