import React, { useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import { gtaWorldAuthService } from '../services/gtaWorldAuthService';

const Login: React.FC = () => {
  const [searchParams] = useSearchParams();

  useEffect(() => {
    // Check if there's a return URL in the query parameters
    const returnUrl = searchParams.get('returnUrl');
    
    // Automatically trigger GTA World login when component mounts
    // Pass the return URL if it exists
    gtaWorldAuthService.initiateLogin(returnUrl || undefined);
  }, [searchParams]);

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-8">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mx-auto mb-4"></div>
            <p className="text-gray-600 dark:text-gray-400">Redirecting to GTA World...</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
