import React from 'react';
import { Link } from 'react-router-dom';
import { AlertCircle, X } from 'lucide-react';
import { useUser } from '../contexts/UserContext';
import { ProfileService } from '../services/profileService';

const ProfileCompletionBanner: React.FC = () => {
  const { user } = useUser();
  const [isVisible, setIsVisible] = React.useState(true);

  // Don't show banner if user is not logged in or doesn't need profile completion
  if (!user || !ProfileService.needsProfileCompletion(user) || !isVisible) {
    return null;
  }

  return (
    <div className="bg-yellow-50 dark:bg-yellow-900/20 border-b border-yellow-200 dark:border-yellow-800">
      <div className="max-w-7xl mx-auto py-3 px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between flex-wrap">
          <div className="w-0 flex-1 flex items-center">
            <AlertCircle className="flex-shrink-0 h-5 w-5 text-yellow-400" />
            <p className="ml-3 font-medium text-sm text-yellow-800 dark:text-yellow-200">
              <span className="md:hidden">Complete your profile to get started!</span>
              <span className="hidden md:inline">
                Welcome to Car Spot! Please complete your profile to access all features.
              </span>
            </p>
          </div>
          <div className="order-3 mt-2 flex-shrink-0 w-full sm:order-2 sm:mt-0 sm:w-auto">
            <Link
              to="/complete-profile"
              className="flex items-center justify-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-yellow-800 bg-yellow-100 hover:bg-yellow-200 dark:bg-yellow-800 dark:text-yellow-200 dark:hover:bg-yellow-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-yellow-500"
            >
              Complete Profile
            </Link>
          </div>
          <div className="order-2 flex-shrink-0 sm:order-3 sm:ml-3">
            <button
              type="button"
              className="-mr-1 flex p-2 rounded-md hover:bg-yellow-100 dark:hover:bg-yellow-800 focus:outline-none focus:ring-2 focus:ring-yellow-500"
              onClick={() => setIsVisible(false)}
            >
              <span className="sr-only">Dismiss</span>
              <X className="h-5 w-5 text-yellow-400" />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileCompletionBanner;
