import React, { useState, useEffect, useCallback } from 'react';
import { Users, MessageCircle, X, Trash2, Search, UserPlus, Check, AlertCircle } from 'lucide-react';
import { DealerService, DealerUserRole, DealerInvitation } from '../services/dealerService';

interface TeamManagementProps {
  dealerAccountId: number;
}

interface User {
  id: number;
  name: string;
  gta_world_username: string;
  discord?: string;
  is_dealer: boolean;
  staff_role?: string;
}

const TeamManagement: React.FC<TeamManagementProps> = ({ dealerAccountId }) => {
  const [teamMembers, setTeamMembers] = useState<DealerUserRole[]>([]);
  const [pendingInvitations, setPendingInvitations] = useState<DealerInvitation[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // Search functionality
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState<User[]>([]);
  const [isSearching, setIsSearching] = useState(false);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [selectedRole, setSelectedRole] = useState<'manager' | 'staff'>('staff');
  const [isAdding, setIsAdding] = useState(false);
  const [searchError, setSearchError] = useState<string | null>(null);
  const [searchSuccess, setSearchSuccess] = useState<string | null>(null);

  useEffect(() => {
    loadTeamData();
  }, [dealerAccountId]);

  const loadTeamData = async () => {
    try {
      setIsLoading(true);
      const [team, invitations] = await Promise.all([
        DealerService.getDealerTeam(dealerAccountId),
        DealerService.getPendingInvitations(dealerAccountId)
      ]);
      setTeamMembers(team);
      setPendingInvitations(invitations);
    } catch (error) {
      setError('Failed to load team data');
      console.error('Error loading team data:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancelInvitation = async (invitationId: number) => {
    try {
      await DealerService.declineInvitation(invitationId);
      setPendingInvitations(prev => prev.filter(inv => inv.id !== invitationId));
    } catch (error) {
      console.error('Error canceling invitation:', error);
    }
  };

  const handleRemoveTeamMember = async (userId: number) => {
    if (window.confirm('Are you sure you want to remove this team member?')) {
      try {
        // This would need to be implemented in the API
        // await DealerService.removeTeamMember(dealerAccountId, userId);
        setTeamMembers(prev => prev.filter(member => member.user_id !== userId));
      } catch (error) {
        console.error('Error removing team member:', error);
      }
    }
  };

  // Search functionality
  const handleSearch = useCallback(async () => {
    if (!searchQuery.trim() || searchQuery.length < 3) return;
    
    setIsSearching(true);
    setSearchError(null);
    setSearchResults([]);
    
    try {
      const results = await DealerService.searchUsers(searchQuery);
      setSearchResults(results);
    } catch (err) {
      setSearchError('Failed to search users');
    } finally {
      setIsSearching(false);
    }
  }, [searchQuery]);

  // Auto-search when user types (with debounce)
  useEffect(() => {
    const timeoutId = setTimeout(() => {
      if (searchQuery.length >= 3) {
        handleSearch();
      } else {
        setSearchResults([]);
      }
    }, 300); // 300ms debounce

    return () => clearTimeout(timeoutId);
  }, [searchQuery, handleSearch]);

  const selectUser = (user: User) => {
    if (user.is_dealer) {
      setSearchError('Cannot invite dealers to your team. Dealers have their own dealership.');
      return;
    }
    setSelectedUser(user);
    setSearchError(null);
  };

  const handleAddMember = async () => {
    if (!selectedUser) return;
    
    setIsAdding(true);
    setSearchError(null);
    
    try {
      await DealerService.addUserToTeam(dealerAccountId, selectedUser.id, selectedRole);
      
      setSearchSuccess('Team member added successfully!');
      setSelectedUser(null);
      setSearchQuery('');
      setSearchResults([]);
      loadTeamData(); // Refresh team data
      
      // Clear success message after a short delay
      setTimeout(() => {
        setSearchSuccess(null);
      }, 3000);
      
    } catch (err) {
      setSearchError('Failed to add team member');
    } finally {
      setIsAdding(false);
    }
  };

  const getRoleDisplayName = (role: string) => {
    switch (role) {
      case 'owner': return 'Owner';
      case 'admin': return 'Administrator';
      case 'manager': return 'Manager';
      case 'staff': return 'Staff Member';
      default: return role;
    }
  };

  const getRoleBadgeColor = (role: string) => {
    switch (role) {
      case 'owner': return 'bg-purple-100 text-purple-800';
      case 'admin': return 'bg-red-100 text-red-800';
      case 'manager': return 'bg-blue-100 text-blue-800';
      case 'staff': return 'bg-gray-100 text-gray-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center py-8">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center py-8">
        <p className="text-red-600">{error}</p>
        <button 
          onClick={loadTeamData}
          className="btn-secondary mt-2"
        >
          Try Again
        </button>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900">Team Management</h2>
          <p className="text-gray-600">Manage your dealer team and invitations</p>
        </div>
        {/* InviteTeamMember 
          dealerAccountId={dealerAccountId}
          onInvitationSent={handleInvitationSent}
        /> */}
      </div>

      {/* Add Team Member */}
      <div className="card">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white">Add Team Member</h3>
        </div>
        
        <div className="space-y-4">
          {/* Search Section */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Search for Users
            </label>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="input-field pl-10"
                placeholder="Type at least 3 characters to search..."
              />
              {isSearching && (
                <div className="absolute right-3 top-1/2 transform -translate-y-1/2">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary-600"></div>
                </div>
              )}
            </div>
            {searchQuery.length > 0 && searchQuery.length < 3 && (
              <p className="text-sm text-gray-500 mt-1">
                Type at least 3 characters to search
              </p>
            )}
          </div>

          {/* Search Results */}
          {searchResults.length > 0 && (
            <div>
              <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
                Search Results ({searchResults.length})
              </h4>
              <div className="space-y-2 max-h-48 overflow-y-auto">
                {searchResults.map((user) => (
                  <div
                    key={user.id}
                    onClick={() => selectUser(user)}
                    className={`p-3 border rounded-lg transition-colors ${
                      user.is_dealer
                        ? 'border-red-200 bg-red-50 dark:bg-red-900/20 cursor-not-allowed opacity-60'
                        : selectedUser?.id === user.id
                        ? 'border-primary-500 bg-primary-50 dark:bg-primary-900/20 cursor-pointer'
                        : 'border-gray-200 dark:border-gray-600 hover:border-gray-300 dark:hover:border-gray-500 cursor-pointer'
                    }`}
                  >
                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center">
                          <Users className="h-4 w-4 text-primary-600 dark:text-primary-400" />
                        </div>
                        <div>
                          <p className="font-medium text-gray-900 dark:text-white">{user.name}</p>
                          <p className="text-sm text-gray-500 dark:text-gray-400">
                            {user.gta_world_username}
                            {user.discord && `  ((${user.discord}))`}
                          </p>
                        </div>
                      </div>
                      <div className="flex items-center space-x-2">
                        {user.is_dealer && (
                          <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200">
                            Dealer
                          </span>
                        )}
                        {user.staff_role && (
                          <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200">
                            {user.staff_role.charAt(0).toUpperCase() + user.staff_role.slice(1)}
                          </span>
                        )}
                        {selectedUser?.id === user.id && !user.is_dealer && (
                          <Check className="h-4 w-4 text-primary-600 dark:text-primary-400" />
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Role Selection */}
          {selectedUser && !selectedUser.is_dealer && (
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Assign Role
              </label>
              <select
                value={selectedRole}
                onChange={(e) => setSelectedRole(e.target.value as 'manager' | 'staff')}
                className="input-field"
              >
                <option value="staff">Staff Member</option>
                <option value="manager">Manager</option>
              </select>
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                {selectedRole === 'manager' 
                  ? 'Managers can manage inventory and view analytics'
                  : 'Staff members can manage inventory'
                }
              </p>
            </div>
          )}

          {/* Error Message */}
          {searchError && (
            <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-md p-3">
              <div className="flex items-center space-x-2">
                <AlertCircle className="h-5 w-5 text-red-600" />
                <span className="text-sm text-red-700 dark:text-red-300">{searchError}</span>
              </div>
            </div>
          )}

          {/* Success Message */}
          {searchSuccess && (
            <div className="bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-md p-3">
              <div className="flex items-center space-x-2">
                <Check className="h-5 w-5 text-green-600" />
                <span className="text-sm text-green-700 dark:text-green-300">{searchSuccess}</span>
              </div>
            </div>
          )}

          {/* Add Button */}
          {selectedUser && !selectedUser.is_dealer && (
            <div className="flex justify-end">
              <button
                onClick={handleAddMember}
                disabled={isAdding}
                className="btn-primary flex items-center space-x-2 disabled:opacity-50"
              >
                <UserPlus className="h-4 w-4" />
                <span>{isAdding ? 'Adding...' : 'Add to Team'}</span>
              </button>
            </div>
          )}
        </div>
      </div>

      {/* Team Members */}
      <div className="card">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-gray-900">Team Members</h3>
          <span className="text-sm text-gray-500">{teamMembers.length} members</span>
        </div>
        
        {teamMembers.length === 0 ? (
          <div className="text-center py-8 text-gray-500">
            <Users className="h-12 w-12 mx-auto mb-3 text-gray-300" />
            <p>No team members yet</p>
            <p className="text-sm">Invite team members to get started</p>
          </div>
        ) : (
          <div className="space-y-3">
            {teamMembers.map((member) => (
              <div key={member.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                <div className="flex items-center space-x-3">
                  <div className="w-8 h-8 bg-primary-100 rounded-full flex items-center justify-center">
                    <Users className="h-4 w-4 text-primary-600" />
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">User ID: {member.user_id}</p>
                    <p className="text-sm text-gray-500">Joined {member.joined_at ? new Date(member.joined_at).toLocaleDateString() : 'Unknown'}</p>
                  </div>
                </div>
                <div className="flex items-center space-x-3">
                  <span className={`px-2 py-1 text-xs font-medium rounded-full ${getRoleBadgeColor(member.role)}`}>
                    {getRoleDisplayName(member.role)}
                  </span>
                  {member.role !== 'owner' && (
                    <button
                      onClick={() => handleRemoveTeamMember(member.user_id)}
                      className="text-red-600 hover:text-red-800 p-1"
                      title="Remove team member"
                    >
                      <Trash2 className="h-4 w-4" />
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Pending Invitations */}
      {pendingInvitations.length > 0 && (
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900">Pending Invitations</h3>
            <span className="text-sm text-gray-500">{pendingInvitations.length} pending</span>
          </div>
          
          <div className="space-y-3">
            {pendingInvitations.map((invitation) => (
              <div key={invitation.id} className="flex items-center justify-between p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                <div className="flex items-center space-x-3">
                  <MessageCircle className="h-5 w-5 text-yellow-600" />
                  <div>
                    <p className="font-medium text-gray-900">(({invitation.discord}))</p>
                    <div className="flex items-center space-x-2 text-sm text-gray-500">
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${getRoleBadgeColor(invitation.role)}`}>
                        {getRoleDisplayName(invitation.role)}
                      </span>
                      <span> Expires {new Date(invitation.expires_at).toLocaleDateString()}</span>
                    </div>
                  </div>
                </div>
                <button
                  onClick={() => invitation.id && handleCancelInvitation(invitation.id)}
                  className="text-red-600 hover:text-red-800 p-1"
                  title="Cancel invitation"
                >
                  <X className="h-4 w-4" />
                </button>
              </div>
            ))}
          </div>
        </div>
      )}


    </div>
  );
};

export default TeamManagement;
