import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { 
  Star, 
  Award, 
  Globe, 
  Calendar, 
  Users, 
  Building,
  MessageSquare,
  Shield,
  TrendingUp,
  Car,
  ShoppingCart,
  ExternalLink,
  DollarSign
} from 'lucide-react';
import { ReviewService, UserStats } from '../services/reviewService';
import MySalesHistory from '../components/MySalesHistory';

const UserProfile: React.FC = () => {
  const { userId } = useParams<{ userId: string }>();
  const [userStats, setUserStats] = useState<UserStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState('overview');

  useEffect(() => {
    if (userId) {
      loadUserStats(parseInt(userId));
    }
  }, [userId]);

  const loadUserStats = async (id: number) => {
    try {
      setLoading(true);
      const stats = await ReviewService.getUserStats(id);
      setUserStats(stats);
    } catch (err) {
      setError('Failed to load user profile');
      console.error('Error loading user stats:', err);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-8">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="animate-pulse">
            <div className="h-32 bg-gray-200 dark:bg-gray-700 rounded-lg mb-6"></div>
            <div className="h-64 bg-gray-200 dark:bg-gray-700 rounded-lg"></div>
          </div>
        </div>
      </div>
    );
  }

  if (error || !userStats) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-8">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
              {error || 'User not found'}
            </h1>
            <Link to="/" className="btn-primary">
              Return to Home
            </Link>
          </div>
        </div>
      </div>
    );
  }

  const renderStars = (rating: number) => {
    return Array.from({ length: 5 }, (_, i) => (
      <Star
        key={i}
        className={`h-5 w-5 ${
          i < rating
            ? 'text-yellow-400 fill-current'
            : 'text-gray-300 dark:text-gray-600'
        }`}
      />
    ));
  };

  const renderAwards = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {userStats.awards.map((userAward) => (
        <div key={userAward.id} className="card p-4">
          <div className="flex items-center space-x-3">
            <div className="text-3xl">{userAward.award?.icon_url}</div>
            <div>
              <h4 className="font-semibold text-gray-900 dark:text-white">
                {userAward.award?.name}
              </h4>
              <p className="text-sm text-gray-600 dark:text-gray-300">
                {userAward.award?.description}
              </p>
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                Awarded {new Date(userAward.awarded_at).toLocaleDateString()}
              </p>
            </div>
          </div>
        </div>
      ))}
    </div>
  );

  const renderReviews = () => (
    <div className="space-y-6">
      {userStats.reviews.map((review) => (
        <div key={review.id} className="card p-6">
          <div className="flex items-start justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="h-10 w-10 rounded-full bg-primary-100 dark:bg-primary-900 flex items-center justify-center">
                <span className="text-sm font-medium text-primary-600 dark:text-primary-400">
                  {review.reviewer_name?.charAt(0).toUpperCase()}
                </span>
              </div>
              <div>
                <h4 className="font-medium text-gray-900 dark:text-white">
                  {review.reviewer_name}
                </h4>
                <p className="text-sm text-gray-500 dark:text-gray-400">
                  {review.reviewer_discord && (
                    <span className="text-sm text-gray-500 dark:text-gray-400">
                      (({review.reviewer_discord}))
                    </span>
                  )}
                </p>
              </div>
            </div>
            <div className="flex items-center space-x-2">
              {renderStars(review.rating)}
              <span className="text-sm text-gray-500 dark:text-gray-400">
                {review.rating}/5
              </span>
            </div>
          </div>
          
          <div className="mb-4">
            <h5 className="font-medium text-gray-900 dark:text-white mb-2">
              {review.title}
            </h5>
            <p className="text-gray-600 dark:text-gray-300">
              {review.comment}
            </p>
          </div>

          <div className="flex items-center justify-between text-sm text-gray-500 dark:text-gray-400">
            <span className="flex items-center space-x-1">
              <Calendar className="h-4 w-4" />
              <span>{new Date(review.created_at!).toLocaleDateString()}</span>
            </span>
            <span className="flex items-center space-x-1">
              {review.review_type === 'buyer' ? (
                <>
                  <ShoppingCart className="h-4 w-4" />
                  <span>Buyer Review</span>
                </>
              ) : (
                <>
                  <Car className="h-4 w-4" />
                  <span>Seller Review</span>
                </>
              )}
            </span>
          </div>

          {/* Dealer Responses */}
          {review.responses && review.responses.length > 0 && (
            <div className="mt-4 pt-4 border-t border-gray-200 dark:border-gray-700">
              {review.responses.map((response) => (
                <div key={response.id} className="bg-blue-50 dark:bg-blue-900/20 rounded-lg p-4">
                  <div className="flex items-center space-x-2 mb-2">
                    <Building className="h-4 w-4 text-blue-600 dark:text-blue-400" />
                    <span className="font-medium text-blue-900 dark:text-blue-100">
                      {response.responder_name}
                    </span>
                    <span className="text-sm text-blue-700 dark:text-blue-300">
                      (Dealer Response)
                    </span>
                  </div>
                  <p className="text-blue-800 dark:text-blue-200">
                    {response.response}
                  </p>
                  <p className="text-xs text-blue-600 dark:text-blue-400 mt-2">
                    {new Date(response.created_at!).toLocaleDateString()}
                  </p>
                </div>
              ))}
            </div>
          )}
        </div>
      ))}
    </div>
  );

  const renderStats = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
      <div className="card p-6 text-center">
        <div className="bg-blue-100 dark:bg-blue-900 rounded-full p-3 mx-auto mb-4 w-12 h-12 flex items-center justify-center">
          <Star className="h-6 w-6 text-blue-600 dark:text-blue-400" />
        </div>
        <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
          {userStats.review_stats.average_rating}
        </h3>
        <p className="text-sm text-gray-600 dark:text-gray-300">Average Rating</p>
      </div>

      <div className="card p-6 text-center">
        <div className="bg-green-100 dark:bg-green-900 rounded-full p-3 mx-auto mb-4 w-12 h-12 flex items-center justify-center">
          <MessageSquare className="h-6 w-6 text-green-600 dark:text-green-400" />
        </div>
        <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
          {userStats.total_reviews}
        </h3>
        <p className="text-sm text-gray-600 dark:text-gray-300">Total Reviews</p>
      </div>

      <div className="card p-6 text-center">
        <div className="bg-yellow-100 dark:bg-yellow-900 rounded-full p-3 mx-auto mb-4 w-12 h-12 flex items-center justify-center">
          <Award className="h-6 w-6 text-yellow-600 dark:text-yellow-400" />
        </div>
        <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
          {userStats.total_awards}
        </h3>
        <p className="text-sm text-gray-600 dark:text-gray-300">Awards Earned</p>
      </div>

      <div className="card p-6 text-center">
        <div className="bg-purple-100 dark:bg-purple-900 rounded-full p-3 mx-auto mb-4 w-12 h-12 flex items-center justify-center">
          <TrendingUp className="h-6 w-6 text-purple-600 dark:text-purple-400" />
        </div>
        <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
          {userStats.profile?.experience_years || 0}
        </h3>
        <p className="text-sm text-gray-600 dark:text-gray-300">Years Experience</p>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-8">
      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="card p-8 mb-8">
          <div className="flex flex-col lg:flex-row items-start lg:items-center space-y-6 lg:space-y-0 lg:space-x-8">
            {/* Avatar */}
            <div className="flex-shrink-0">
              <img
                src={userStats.profile?.avatar_url || 'https://via.placeholder.com/150'}
                alt={userStats.name}
                className="h-32 w-32 rounded-full object-cover border-4 border-white dark:border-gray-800 shadow-lg"
              />
            </div>

            {/* User Info */}
            <div className="flex-1 min-w-0">
              <div className="flex items-center space-x-3 mb-2">
                {userStats.is_dealer ? (
                  <Building className="h-6 w-6 text-primary-600 dark:text-primary-400" />
                ) : (
                  <Users className="h-6 w-6 text-primary-600 dark:text-primary-400" />
                )}
                <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
                  {userStats.name}
                </h1>
                {userStats.is_dealer && (
                  <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-primary-100 dark:bg-primary-900 text-primary-800 dark:text-primary-200">
                    <Shield className="h-4 w-4 mr-1" />
                    Verified Dealer
                  </span>
                )}
              </div>

              {userStats.company_name && (
                <p className="text-xl text-gray-600 dark:text-gray-300 mb-3">
                  {userStats.company_name}
                </p>
              )}

              <div className="flex items-center space-x-6 mb-4">
                <div className="flex items-center space-x-2">
                  {renderStars(userStats.rating)}
                  <span className="text-lg font-semibold text-gray-900 dark:text-white">
                    {userStats.rating}
                  </span>
                  <span className="text-gray-500 dark:text-gray-400">
                    ({userStats.total_reviews} reviews)
                  </span>
                </div>
              </div>

              {userStats.profile?.bio && (
                <p className="text-gray-600 dark:text-gray-300 mb-4">
                  {userStats.profile.bio}
                </p>
              )}

              {/* Quick Stats */}
              <div className="flex flex-wrap items-center space-x-6 text-sm text-gray-500 dark:text-gray-400">

                {userStats.profile?.website && (
                  <a
                    href={userStats.profile.website}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex items-center space-x-1 text-primary-600 dark:text-primary-400 hover:text-primary-700 dark:hover:text-primary-300"
                  >
                    <Globe className="h-4 w-4" />
                    <span>Website</span>
                    <ExternalLink className="h-3 w-3" />
                  </a>
                )}
                <span className="flex items-center space-x-1">
                  <Calendar className="h-4 w-4" />
                  <span>Member since {userStats.profile?.member_since?.toLocaleDateString() || 'Unknown'}</span>
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* Navigation Tabs */}
        <div className="border-b border-gray-200 dark:border-gray-700 mb-8">
          <nav className="flex space-x-8">
            {[
              { id: 'overview', label: 'Overview', icon: TrendingUp },
              { id: 'reviews', label: 'Reviews', icon: MessageSquare },
              { id: 'awards', label: 'Awards', icon: Award },
              { id: 'sales', label: 'Sales History', icon: DollarSign },
            ].map((tab) => (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id)}
                className={`flex items-center space-x-2 py-4 px-1 border-b-2 font-medium text-sm ${
                  activeTab === tab.id
                    ? 'border-primary-500 text-primary-600 dark:text-primary-400'
                    : 'border-transparent text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-300 hover:border-gray-300 dark:hover:border-gray-600'
                }`}
              >
                <tab.icon className="h-4 w-4" />
                <span>{tab.label}</span>
              </button>
            ))}
          </nav>
        </div>

        {/* Tab Content */}
        <div className="space-y-8">
          {activeTab === 'overview' && (
            <>
              {renderStats()}
              
              {/* Specialties */}
              {userStats.profile?.specialties && userStats.profile.specialties.length > 0 && (
                <div className="card p-6">
                  <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                    Specialties
                  </h3>
                  <div className="flex flex-wrap gap-2">
                    {userStats.profile.specialties.map((specialty, index) => (
                      <span
                        key={index}
                        className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-primary-100 dark:bg-primary-900 text-primary-800 dark:text-primary-200"
                      >
                        {specialty}
                      </span>
                    ))}
                  </div>
                </div>
              )}

              {/* Recent Reviews Preview */}
              <div className="card p-6">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                  Recent Reviews
                </h3>
                <div className="space-y-4">
                  {userStats.reviews.slice(0, 3).map((review) => (
                    <div key={review.id} className="border-b border-gray-200 dark:border-gray-700 pb-4 last:border-b-0">
                      <div className="flex items-center justify-between mb-2">
                        <div className="flex items-center space-x-2">
                          <span className="font-medium text-gray-900 dark:text-white">
                            {review.reviewer_name}
                          </span>
                          <div className="flex items-center space-x-1">
                            {renderStars(review.rating)}
                          </div>
                        </div>
                        <span className="text-sm text-gray-500 dark:text-gray-400">
                          {new Date(review.created_at!).toLocaleDateString()}
                        </span>
                      </div>
                      <p className="text-gray-600 dark:text-gray-300 text-sm">
                        {review.title}
                      </p>
                    </div>
                  ))}
                </div>
                <div className="mt-4">
                  <button
                    onClick={() => setActiveTab('reviews')}
                    className="text-primary-600 dark:text-primary-400 hover:text-primary-700 dark:hover:text-primary-300 font-medium"
                  >
                    View all reviews 
                  </button>
                </div>
              </div>
            </>
          )}

          {activeTab === 'reviews' && (
            <div className="card p-6">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Reviews ({userStats.reviews.length})
                </h3>
                <div className="flex items-center space-x-4 text-sm text-gray-500 dark:text-gray-400">
                  <span>Average: {userStats.review_stats.average_rating}/5</span>
                  <span></span>
                  <span>{userStats.review_stats.total_buyer_reviews} buyer reviews</span>
                  <span></span>
                  <span>{userStats.review_stats.total_seller_reviews} seller reviews</span>
                </div>
              </div>
              {renderReviews()}
            </div>
          )}

          {activeTab === 'awards' && (
            <div className="card p-6">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Awards ({userStats.awards.length})
                </h3>
                <div className="text-sm text-gray-500 dark:text-gray-400">
                  Recognition for outstanding performance
                </div>
              </div>
              {renderAwards()}
            </div>
          )}

          {activeTab === 'sales' && (
            <div className="card p-6">
              <MySalesHistory userId={parseInt(userId!)} />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default UserProfile;
