import React, { useState, useEffect, useRef } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useUser } from '../contexts/UserContext';
import DarkModeToggle from './DarkModeToggle';
import { gtaWorldAuthService } from '../services/gtaWorldAuthService';
import { ProfileService } from '../services/profileService';
import { 
  Menu, 
  X, 
  User, 
  LogOut, 
  Store, 
  DollarSign,
  Shield,
  Car,
  MessageSquare,
  ChevronDown,
  AlertCircle
} from 'lucide-react';

const Navbar: React.FC = () => {
  const { user, logout } = useUser();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isUserDropdownOpen, setIsUserDropdownOpen] = useState(false);
  const location = useLocation();
  const userDropdownRef = useRef<HTMLDivElement>(null);

  const isDealer = user?.is_dealer || false;
  


  // Handle clicking outside the user dropdown and escape key
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (userDropdownRef.current && !userDropdownRef.current.contains(event.target as Node)) {
        setIsUserDropdownOpen(false);
      }
    };

    const handleEscapeKey = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        setIsUserDropdownOpen(false);
      }
    };

    if (isUserDropdownOpen) {
      document.addEventListener('mousedown', handleClickOutside);
      document.addEventListener('keydown', handleEscapeKey);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
      document.removeEventListener('keydown', handleEscapeKey);
    };
  }, [isUserDropdownOpen]);

  const navigation = [
    { name: 'Browse Cars', href: '/browse', current: location.pathname === '/browse' },
    { name: 'Sell Car', href: '/sell', current: location.pathname === '/sell' },
  ];

  const handleLogout = async () => {
    try {
      await logout();
      setIsMenuOpen(false);
      setIsUserDropdownOpen(false);
      // Redirect to home page after successful logout
      window.location.href = '/';
    } catch (error) {
      console.error('Logout error:', error);
      // Still close menus and redirect even if logout fails
      setIsMenuOpen(false);
      setIsUserDropdownOpen(false);
      window.location.href = '/';
    }
  };

  const handleLogin = () => {
    // Close mobile menu if open
    setIsMenuOpen(false);
    // Store current page URL and redirect to login
    gtaWorldAuthService.initiateLogin(location.pathname);
  };

  const toggleUserDropdown = () => {
    setIsUserDropdownOpen(!isUserDropdownOpen);
  };

  const closeUserDropdown = () => {
    setIsUserDropdownOpen(false);
  };

  return (
    <nav className="bg-white dark:bg-gray-800 shadow-sm border-b border-gray-200 dark:border-gray-700">
      <div className="w-full">
        <div className="flex items-center h-16">
          {/* Left side - Logo and dark mode toggle */}
          <div className="flex items-center space-x-4 pl-2">
            <Link to="/" className="flex-shrink-0 flex items-center">
              <div className="w-10 h-10 flex items-center justify-center mr-2">
                <Car className="h-6 w-6 text-blue-600" />
              </div>
              <span className="text-xl font-bold">
                <span className="text-blue-600">CAR</span>
                <span className="text-white">SPOT</span>
              </span>
            </Link>
            
            {/* Dark mode toggle - positioned on the left next to logo */}
            <DarkModeToggle />
          </div>
          
          {/* Center Navigation - Perfectly centered */}
          <div className="hidden md:flex items-center absolute left-1/2 transform -translate-x-1/2">
            <div className="flex items-center space-x-4">
              {navigation.map((item) => (
                <Link
                  key={item.name}
                  to={item.href}
                  className={`inline-flex items-center justify-center px-4 py-2 rounded-lg font-medium text-sm whitespace-nowrap transition-colors duration-200 ${
                    item.current
                      ? 'bg-primary-600 text-white shadow-lg'
                      : 'bg-primary-600 text-white hover:bg-primary-700 hover:shadow-lg'
                  }`}
                >
                  {item.name}
                </Link>
              ))}
            </div>
          </div>

          {/* Right side - User menu */}
          <div className="flex items-center space-x-4 pr-2 ml-auto">
            {/* User menu */}
            {user ? (
              <div className="flex items-center space-x-3">

                


                {/* User dropdown */}
                <div className="relative" ref={userDropdownRef}>
                  <button 
                    onClick={toggleUserDropdown}
                    className="flex items-center space-x-2 text-gray-700 dark:text-gray-300 hover:text-primary-600 dark:hover:text-primary-400 p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2 dark:focus:ring-offset-gray-800 transition-all duration-200"
                  >
                    <div className="w-8 h-8 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center">
                      {user.avatar_url ? (
                        <img
                          src={user.avatar_url}
                          alt={user.name}
                          className="w-6 h-6 rounded-full object-cover"
                        />
                      ) : (
                        <User className="h-4 w-4 text-primary-600 dark:text-primary-400" />
                      )}
                    </div>
                    <div className="hidden sm:block text-left">
                      <div className="font-medium">Hi, {user.name}</div>
                      {user.staff_role && (
                        <div className="text-xs text-purple-600 dark:text-purple-400 font-medium">
                          {user.staff_role}
                        </div>
                      )}

                    </div>
                    <ChevronDown className={`h-4 w-4 transition-transform duration-200 ${isUserDropdownOpen ? 'rotate-180' : ''}`} />
                  </button>
                  
                  {/* Dropdown menu */}
                  <div className={`absolute right-0 mt-2 w-48 bg-white dark:bg-gray-800 rounded-lg shadow-lg border border-gray-200 dark:border-gray-700 py-1 z-50 transition-all duration-200 ease-in-out ${
                    isUserDropdownOpen 
                      ? 'opacity-100 scale-100 translate-y-0' 
                      : 'opacity-0 scale-95 -translate-y-2 pointer-events-none'
                  }`}>
                    {/* Profile completion notification */}
                    {ProfileService.needsProfileCompletion(user) && (
                      <div className="px-4 py-2 bg-yellow-50 dark:bg-yellow-900/20 border-b border-yellow-200 dark:border-yellow-800">
                        <div className="flex items-center space-x-2 text-yellow-800 dark:text-yellow-200">
                          <AlertCircle className="h-4 w-4" />
                          <span className="text-xs font-medium">Complete your profile</span>
                        </div>
                        <Link
                          to="/complete-profile"
                          className="block mt-1 text-xs text-yellow-600 dark:text-yellow-400 hover:text-yellow-800 dark:hover:text-yellow-200"
                          onClick={closeUserDropdown}
                        >
                          Click here to continue 
                        </Link>
                      </div>
                    )}
                    
                    <Link
                      to="/profile"
                      className="block px-4 py-2 text-sm text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-150"
                      onClick={closeUserDropdown}
                    >
                      <div className="flex items-center space-x-2">
                        <User className="h-4 w-4" />
                        <span>Profile</span>
                      </div>
                    </Link>
                    
                    {/* Dealer Portal - Desktop */}
                    {user?.is_dealer && (
                      <Link
                        to="/dealer"
                        className="block px-4 py-2 text-sm text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-150"
                        onClick={closeUserDropdown}
                      >
                        <div className="flex items-center space-x-2">
                          <Store className="h-4 w-4" />
                          <span>Dealer Portal</span>
                        </div>
                      </Link>
                    )}
                    {!isDealer && (
                      <Link
                        to="/become-dealer"
                        className="block px-4 py-2 text-sm text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-150"
                        onClick={closeUserDropdown}
                      >
                        <div className="flex items-center space-x-2">
                          <Store className="h-4 w-4" />
                          <span>Become a Dealer</span>
                        </div>
                      </Link>
                    )}
                    <Link
                      to="/support"
                      className="block px-4 py-2 text-sm text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-150"
                      onClick={closeUserDropdown}
                    >
                      <div className="flex items-center space-x-2">
                        <MessageSquare className="h-4 w-4" />
                        <span>My Tickets</span>
                      </div>
                    </Link>
                    <Link
                      to="/payment-history"
                      className="block px-4 py-2 text-sm text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-150"
                      onClick={closeUserDropdown}
                    >
                      <div className="flex items-center space-x-2">
                        <DollarSign className="h-4 w-4" />
                        <span>Payment History</span>
                      </div>
                    </Link>
                    
                    {/* Admin/Staff Portal - Desktop */}
                    {user?.staff_role && (
                      <Link
                        to="/staff"
                        className="block px-4 py-2 text-sm text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-150"
                        onClick={closeUserDropdown}
                      >
                        <div className="flex items-center space-x-2">
                          <Shield className="h-4 w-4" />
                          <span>{user.staff_role === 'admin' ? 'Admin Portal' : 'Staff Portal'}</span>
                        </div>
                      </Link>
                    )}
                    


                    
                    <button
                      onClick={handleLogout}
                      className="block w-full text-left px-4 py-2 text-sm text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-150"
                    >
                      <div className="flex items-center space-x-2">
                        <LogOut className="h-4 w-4" />
                        <span>Sign Out</span>
                      </div>
                    </button>
                  </div>
                </div>
              </div>
            ) : (
              <button onClick={handleLogin} className="btn-primary flex items-center space-x-2">
                <User className="h-4 w-4" />
                <span>Login</span>
              </button>
            )}

            {/* Mobile menu button */}
            <div className="md:hidden">
              <button
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                className="text-gray-700 dark:text-gray-300 hover:text-primary-600 dark:hover:text-primary-400 p-2"
              >
                {isMenuOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
              </button>
            </div>
          </div>
        </div>

        {/* Mobile Navigation */}
        {isMenuOpen && (
          <div className="md:hidden">
            <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3 bg-white dark:bg-gray-800 border-t border-gray-200 dark:border-gray-700">
              {navigation.map((item) => (
                <Link
                  key={item.name}
                  to={item.href}
                  className={`block px-3 py-2 rounded-md text-base font-medium whitespace-nowrap ${
                    item.current
                      ? 'text-primary-600 bg-primary-50 dark:bg-primary-900 dark:text-primary-400'
                      : 'text-gray-700 dark:text-gray-300 hover:text-primary-600 dark:hover:text-primary-400 hover:bg-gray-50 dark:hover:bg-gray-700'
                  }`}
                  onClick={() => setIsMenuOpen(false)}
                >
                  {item.name}
                </Link>
              ))}
              <div className="pt-4 space-y-2">
                <div className="flex justify-center">
                  <DarkModeToggle />
                </div>
                

                
                {user ? (
                  <div className="space-y-2">
                    <div className="text-center text-sm text-gray-700 dark:text-gray-300 py-2">
                      Hi, {user.name}
                      {user.staff_role && (
                        <div className="mt-1">
                          <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200">
                            {user.staff_role}
                          </span>
                        </div>
                      )}
                    </div>
                    <Link
                      to="/payment-history"
                      className="w-full btn-secondary flex items-center justify-center space-x-2"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      <DollarSign className="h-4 w-4" />
                      <span>Payment History</span>
                    </Link>
                    
                    {/* Admin/Staff Navigation - Mobile */}
                    {user?.staff_role && (
                      <Link
                        to="/staff"
                        className="w-full btn-secondary flex items-center justify-center space-x-2"
                        onClick={() => setIsMenuOpen(false)}
                      >
                        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
                        </svg>
                        <span>{user.staff_role === 'admin' ? 'Admin Portal' : 'Staff Portal'}</span>
                      </Link>
                    )}
                    
                    <Link
                      to="/profile"
                      className="w-full btn-secondary flex items-center justify-center space-x-2"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      <User className="h-4 w-4" />
                      <span>Profile</span>
                    </Link>
                    
                    {!isDealer && (
                      <Link
                        to="/support"
                        className="w-full btn-secondary flex items-center justify-center space-x-2"
                        onClick={() => setIsMenuOpen(false)}
                      >
                        <MessageSquare className="h-4 w-4" />
                        <span>My Tickets</span>
                      </Link>
                    )}
                    
                    <button
                      onClick={handleLogout}
                      className="w-full btn-secondary flex items-center justify-center space-x-4"
                    >
                      <LogOut className="h-4 w-4" />
                      <span>Sign Out</span>
                    </button>
                  </div>
                ) : (
                  <button
                    onClick={handleLogin}
                    className="w-full btn-primary flex items-center justify-center space-x-2"
                  >
                    <User className="h-4 w-4" />
                    <span>Login</span>
                  </button>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
