// Payment Service for Car Spot
// Handles car listings, featured cars, dealer memberships, and payments

import { buildApiUrl, ADMIN_ENDPOINTS, debugApiCall } from '../config/api';

export interface PaymentPlan {
  id: number;
  name: string;
  type: 'listing' | 'featured' | 'membership';
  price: number;
  duration_hours?: number;
  duration_days?: number;
  description: string;
  is_active: boolean;
}

export interface CarListingPayment {
  id?: number;
  car_id: number;
  user_id: number;
  plan_id: number;
  amount: number;
  payment_status: 'pending' | 'paid' | 'refunded' | 'failed';
  listing_start_date: Date;
  listing_end_date: Date;
  gta_world_transaction_id?: string;
}

export interface FeaturedCarPayment {
  id?: number;
  car_id: number;
  user_id: number;
  start_time: Date;
  end_time: Date;
  status: 'active' | 'expired' | 'cancelled';
  payment_status: 'pending' | 'paid' | 'refunded';
}

export interface DealerMembership {
  id?: number;
  dealer_account_id: number;
  status: 'active' | 'expired' | 'cancelled' | 'pending_payment';
  start_date: Date;
  end_date: Date;
  monthly_fee: number;
  last_payment_date?: Date;
  next_payment_date: Date;
  grace_period_end: Date;
}

export interface PaymentTransaction {
  id?: number;
  user_id: number;
  amount: number;
  currency: string;
  payment_type: 'listing' | 'featured' | 'membership' | 'renewal';
  reference_id: number;
  reference_table: string;
  gta_world_transaction_id?: string;
  status: 'pending' | 'completed' | 'failed' | 'cancelled' | 'refunded';
  payment_method: string;
  created_at: string | Date;
  metadata?: any;
}

export interface UserSubscription {
  id?: number;
  user_id: number;
  plan_id: number;
  plan_name: string;
  plan_type: string;
  price: number;
  start_date: string | Date;
  end_date: string | Date;
  status: 'active' | 'expired' | 'cancelled' | 'pending_payment';
}

export interface FeaturedCar {
  id?: number;
  car_id: number;
  name: string;
  make: string;
  model: string;
  year: number;
  start_time: string | Date;
  end_time: string | Date;
  status: 'active' | 'expired' | 'cancelled';
}

export interface AdminSetting {
  id?: number;
  setting_key: string;
  setting_value: string;
  setting_type: 'string' | 'number' | 'boolean' | 'json';
  description: string;
  is_editable: boolean;
}

// Payment configuration interface
export interface PaymentConfig {
  introductory_rate: number;        // $500 for first-time sellers
  regular_listing_price: number;    // $1,500 for returning sellers
  featured_car_price: number;       // $100 for featured cars
  dealer_membership_price: number;       // $25,000/month for dealers
  dealer_free_featured_per_month: number; // 1 free featured car per month
  listing_duration_days: number;    // 30 days
  featured_duration_hours: number;  // 24 hours
  membership_grace_period_days: number; // 30 days grace period
}

export const PaymentService = {
  // Get all payment plans
  async getPaymentPlans(): Promise<PaymentPlan[]> {
    try {
      const response = await fetch('/api/payments.php?action=plans');
      if (!response.ok) throw new Error('Failed to fetch payment plans');
      return await response.json();
    } catch (error) {
      console.error('Error fetching payment plans:', error);
      throw error;
    }
  },

  // Get payment plan by type
  async getPaymentPlanByType(type: 'listing' | 'featured' | 'membership'): Promise<PaymentPlan | null> {
    try {
      const response = await fetch(`/api/payments.php?action=plan_by_type&type=${type}`);
      if (!response.ok) throw new Error('Failed to fetch payment plan');
      return await response.json();
    } catch (error) {
      console.error('Error fetching payment plan:', error);
      throw error;
    }
  },

  // Purchase car listing
  async purchaseCarListing(carId: number, userId: number): Promise<CarListingPayment> {
    try {
      const response = await fetch('/api/payments.php?action=purchase_listing', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ car_id: carId, user_id: userId })
      });
      
      if (!response.ok) throw new Error('Failed to purchase car listing');
      return await response.json();
    } catch (error) {
      console.error('Error purchasing car listing:', error);
      throw error;
    }
  },

  // Purchase featured car
  async purchaseFeaturedCar(carId: number, userId: number): Promise<FeaturedCarPayment> {
    try {
      const response = await fetch('/api/payments.php?action=purchase_featured', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ car_id: carId, user_id: userId })
      });
      
      if (!response.ok) throw new Error('Failed to purchase featured car');
      return await response.json();
    } catch (error) {
      console.error('Error purchasing featured car:', error);
      throw error;
    }
  },

  // Purchase dealer membership
  async purchaseDealerMembership(dealerAccountId: number, userId: number): Promise<DealerMembership> {
    try {
      const response = await fetch('/api/payments.php?action=purchase_membership', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ dealer_account_id: dealerAccountId, user_id: userId })
      });
      
      if (!response.ok) throw new Error('Failed to purchase dealer membership');
      return await response.json();
    } catch (error) {
      console.error('Error purchasing dealer membership:', error);
      throw error;
    }
    },

  // Renew dealer membership
  async renewDealerMembership(membershipId: number, userId: number): Promise<DealerMembership> {
    try {
      const response = await fetch('/api/payments.php?action=renew_membership', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ membership_id: membershipId, user_id: userId })
      });
      
      if (!response.ok) throw new Error('Failed to renew dealer membership');
      return await response.json();
    } catch (error) {
      console.error('Error renewing dealer membership:', error);
      throw error;
    }
  },

  // Get user's active subscriptions
  async getUserSubscriptions(userId: number): Promise<any[]> {
    try {
      const response = await fetch(`/api/payments.php?action=user_subscriptions&user_id=${userId}`);
      if (!response.ok) throw new Error('Failed to fetch user subscriptions');
      return await response.json();
    } catch (error) {
      console.error('Error fetching user subscriptions:', error);
      throw error;
    }
  },

  // Get dealer membership status
  async getDealerMembership(dealerAccountId: number): Promise<DealerMembership | null> {
    try {
      const response = await fetch(`/api/payments.php?action=dealer_membership&dealer_id=${dealerAccountId}`);
      if (!response.ok) throw new Error('Failed to fetch dealer membership');
      return await response.json();
    } catch (error) {
      console.error('Error fetching dealer membership:', error);
      throw error;
    }
  },

  // Get featured cars
  async getFeaturedCars(): Promise<any[]> {
    try {
      const response = await fetch('/api/payments.php?action=featured_cars');
      if (!response.ok) throw new Error('Failed to fetch featured cars');
      return await response.json();
    } catch (error) {
      console.error('Error fetching featured cars:', error);
      throw error;
    }
  },

  // Process GTA World payment
  async processGTAWorldPayment(paymentData: {
    amount: number;
    user_id: number;
    payment_type: string;
    reference_id: number;
    reference_table: string;
  }): Promise<PaymentTransaction> {
    try {
      const response = await fetch('/api/payments.php?action=process_gta_world_payment', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(paymentData)
      });
      
      if (!response.ok) throw new Error('Failed to process GTA World payment');
      return await response.json();
    } catch (error) {
      console.error('Error processing GTA World payment:', error);
      throw error;
    }
  },

  // Get payment transaction by ID
  async getPaymentTransaction(transactionId: number): Promise<PaymentTransaction | null> {
    try {
      const response = await fetch(`/api/payments.php?action=transaction&id=${transactionId}`);
      if (!response.ok) throw new Error('Failed to fetch payment transaction');
      return await response.json();
    } catch (error) {
      console.error('Error fetching payment transaction:', error);
      throw error;
    }
  },

  // Get admin settings
  async getAdminSettings(): Promise<AdminSetting[]> {
    try {
      const url = buildApiUrl(ADMIN_ENDPOINTS.SETTINGS);
      debugApiCall(ADMIN_ENDPOINTS.SETTINGS, url);
      
      const response = await fetch(url);
      console.log('Admin settings response status:', response.status);
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error('Admin settings error response:', errorText);
        throw new Error(`Failed to fetch admin settings: ${response.status} ${response.statusText}`);
      }
      
      const data = await response.json();
      console.log('Admin settings data received:', data);
      return data;
    } catch (error) {
      console.error('Error fetching admin settings:', error);
      throw error;
    }
  },

  // Update admin setting
  async updateAdminSetting(settingKey: string, settingValue: string): Promise<AdminSetting> {
    try {
      const response = await fetch(buildApiUrl(ADMIN_ENDPOINTS.UPDATE_SETTING), {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ setting_key: settingKey, setting_value: settingValue })
      });
      
      if (!response.ok) throw new Error('Failed to update admin setting');
      return await response.json();
    } catch (error) {
      console.error('Error updating admin setting:', error);
      throw error;
    }
  },

  // Check if user can list car (has active membership or can pay for listing)
  async canUserListCar(userId: number, dealerAccountId?: number): Promise<{
    can_list: boolean;
    reason?: string;
    membership?: DealerMembership;
    requires_payment?: boolean;
    payment_amount?: number;
  }> {
    try {
      const response = await fetch(`/api/payments.php?action=can_list_car&user_id=${userId}&dealer_id=${dealerAccountId || ''}`);
      if (!response.ok) throw new Error('Failed to check listing permissions');
      return await response.json();
    } catch (error) {
      console.error('Error checking listing permissions:', error);
      throw error;
    }
  },

  // Get user's payment history
  async getUserPaymentHistory(userId: number): Promise<PaymentTransaction[]> {
    try {
      const response = await fetch(`/api/payments.php?action=payment_history&user_id=${userId}`);
      if (!response.ok) throw new Error('Failed to fetch payment history');
      return await response.json();
    } catch (error) {
      console.error('Error fetching payment history:', error);
      throw error;
    }
  }
};
