import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { gtaWorldAuthService } from '../services/gtaWorldAuthService';

export interface User {
  id: number;
  name: string;
  email?: string;
  discord?: string | null;
  phone_number?: string;
  routing_number?: string;
  avatar_url?: string;
  is_dealer: boolean;
  staff_role?: 'admin' | 'moderator' | 'support' | 'analyst';
  company_name?: string;
  gta_world_id: number;
  gta_world_username: string;
  created_at?: string;
}

interface UserContextType {
  user: User | null;
  login: (userData: User) => Promise<void>;
  logout: () => void;
  isLoading: boolean;
  checkSession: () => Promise<boolean>;
  forceClearSession: () => void;
  refreshUser: () => Promise<void>;
}

const UserContext = createContext<UserContextType | undefined>(undefined);

export const useUser = () => {
  const context = useContext(UserContext);
  if (context === undefined) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
};

interface UserProviderProps {
  children: ReactNode;
}

export const UserProvider: React.FC<UserProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const checkSession = async () => {
      try {
        // Check if user is logged in from localStorage
        const savedUser = localStorage.getItem('user');
        if (savedUser) {
          try {
            const userData = JSON.parse(savedUser);
            
            // Verify session is still valid on backend
            const response = await fetch('/api/auth/check-session.php', {
              method: 'GET',
              credentials: 'include',
            });
            
            if (response.ok) {
              const sessionData = await response.json();
              if (sessionData.user) {
                setUser(sessionData.user);
                localStorage.setItem('user', JSON.stringify(sessionData.user));
              } else {
                // Session expired, clear local state
                localStorage.removeItem('user');
                setUser(null);
              }
            } else {
              // Session check failed, clear local state
              localStorage.removeItem('user');
              setUser(null);
            }
          } catch (error) {
            console.error('Error parsing saved user:', error);
            localStorage.removeItem('user');
            setUser(null);
          }
        }
      } catch (error) {
        console.error('Session check error:', error);
        // Clear user data on error
        localStorage.removeItem('user');
        setUser(null);
      } finally {
        setIsLoading(false);
      }
    };
    
    checkSession();
  }, []);

  const login = async (userData: User) => {
    // Always fetch fresh user data from server on login
    try {
      const response = await fetch('/api/auth/check-session.php', {
        method: 'GET',
        credentials: 'include',
      });
      
      if (response.ok) {
        const sessionData = await response.json();
        if (sessionData.user) {
          setUser(sessionData.user);
          localStorage.setItem('user', JSON.stringify(sessionData.user));
          return;
        }
      }
    } catch (error) {
      console.error('Error fetching fresh user data on login:', error);
    }
    
    // Fallback to provided userData if API call fails
    setUser(userData);
    localStorage.setItem('user', JSON.stringify(userData));
  };

  const checkSession = async (): Promise<boolean> => {
    try {
      const response = await fetch('/api/auth/check-session.php', {
        method: 'GET',
        credentials: 'include',
      });
      
      if (response.ok) {
        const sessionData = await response.json();
        return sessionData.authenticated;
      }
      return false;
    } catch (error) {
      console.error('Session check error:', error);
      return false;
    }
  };

  const forceClearSession = () => {
    // Force clear all session data
    setUser(null);
    localStorage.removeItem('user');
    gtaWorldAuthService.logout();
    
    // Clear all cookies
    document.cookie.split(";").forEach(function(c) { 
      document.cookie = c.replace(/^ +/, "").replace(/=.*/, "=;expires=" + new Date().toUTCString() + ";path=/"); 
    });
    
    // Clear session storage
    sessionStorage.clear();
  };

  const logout = async () => {
    try {
      // Call backend logout endpoint to clear PHP session
      const response = await fetch('/api/auth/logout.php', {
        method: 'POST',
        credentials: 'include', // Include cookies for session
      });
      
      if (!response.ok) {
        throw new Error('Logout failed');
      }
      
      // Use GTA World auth service to clear all tokens and data
      gtaWorldAuthService.logout();
      
    } catch (error) {
      console.error('Error calling logout endpoint:', error);
      // Continue with frontend logout even if backend fails
    }
    
    // Clear frontend state
    setUser(null);
    localStorage.removeItem('user');
    
    // Force clear any remaining session data
    document.cookie.split(";").forEach(function(c) { 
      document.cookie = c.replace(/^ +/, "").replace(/=.*/, "=;expires=" + new Date().toUTCString() + ";path=/"); 
    });
  };

  const refreshUser = async () => {
    try {
      const response = await fetch('/api/auth/check-session.php', {
        method: 'GET',
        credentials: 'include',
      });
      
      if (response.ok) {
        const sessionData = await response.json();
        if (sessionData.user) {
          setUser(sessionData.user);
          localStorage.setItem('user', JSON.stringify(sessionData.user));
        }
      }
    } catch (error) {
      console.error('Error refreshing user data:', error);
    }
  };

  const value: UserContextType = {
    user,
    login,
    logout,
    isLoading,
    checkSession,
    forceClearSession,
    refreshUser,
  };

  return (
    <UserContext.Provider value={value}>
      {children}
    </UserContext.Provider>
  );
};
