import React, { useState, useEffect } from 'react';
import { 
  Webhook, 
  TestTube, 
  Copy, 
  CheckCircle, 
  AlertCircle,
  Lock,
  Globe,
  Settings,
  RefreshCw
} from 'lucide-react';

interface WebhookConfig {
  id: string;
  name: string;
  description: string;
  type: 'public' | 'private' | 'system';
  url: string;
  enabled: boolean;
  messageTemplate: string;
  exampleData: any;
}

const WebhookManagement: React.FC = () => {
  const [webhooks, setWebhooks] = useState<WebhookConfig[]>([]);
  const [showTestModal, setShowTestModal] = useState(false);
  const [testResults, setTestResults] = useState<any[]>([]);
  const [testing, setTesting] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    loadWebhooks();
  }, []);

  const loadWebhooks = async () => {
    try {
      setLoading(true);
      setError(null);
      
      console.log('Loading webhooks from database...');
      
      // Load webhook configurations from database
      const response = await fetch('/api/webhooks.php?action=get_configs');
      console.log('Webhook API response status:', response.status);
      
      if (response.ok) {
        const result = await response.json();
        console.log('Webhook API result:', result);
        
        if (result.success && result.configs && result.configs.length > 0) {
          // Transform database format to component format
          const transformedWebhooks = result.configs.map((config: any) => ({
            id: config.webhook_id,
            name: config.name,
            description: config.description,
            type: config.type,
            url: config.url || '',
            enabled: config.enabled !== undefined ? config.enabled : true,
            messageTemplate: config.message_template || '',
            exampleData: JSON.parse(config.example_data || '{}')
          }));
          setWebhooks(transformedWebhooks);
          console.log('Loaded webhooks from database:', transformedWebhooks);
        } else {
          console.log('No webhooks found in database, using defaults');
          // Fall back to default webhooks
          loadDefaultWebhooks();
        }
      } else {
        const errorText = await response.text();
        console.error('Failed to load webhooks from database:', response.status, response.statusText, errorText);
        setError(`Failed to load webhooks: ${response.status} ${response.statusText}`);
        // Fall back to default webhooks
        loadDefaultWebhooks();
      }
    } catch (error) {
      console.error('Error loading webhooks from database, using defaults:', error);
      setError(`Error loading webhooks: ${error instanceof Error ? error.message : 'Unknown error'}`);
      // Fall back to default webhooks
      loadDefaultWebhooks();
    } finally {
      setLoading(false);
    }
  };

  const loadDefaultWebhooks = () => {
    // Initialize with default webhook configurations
    const defaultWebhooks: WebhookConfig[] = [
      // Public Webhooks
      {
        id: 'new-postings',
        name: 'New Postings',
        description: 'Triggered when a user or dealer posts a new vehicle',
        type: 'public',
        url: '',
        enabled: true,
        messageTemplate: ' **{username}** posted a new vehicle!\n**{make} {model}** - ${price}\n[View Posting]({posting_url})',
        exampleData: {
          username: 'JohnDoe',
          make: 'BMW',
          model: 'M3',
          price: '45,000',
          posting_url: 'https://carspot.site/cars/123'
        }
      },
      {
        id: 'new-featured',
        name: 'New Featured',
        description: 'Triggered when a vehicle becomes featured',
        type: 'public',
        url: '',
        enabled: true,
        messageTemplate: ' **{username}** has a new featured vehicle!\n**{make} {model}** - ${price}\n[View Featured Posting]({posting_url})',
        exampleData: {
          username: 'PremiumDealer',
          make: 'Mercedes',
          model: 'AMG GT',
          price: '125,000',
          posting_url: 'https://carspot.site/cars/456'
        }
      },
      {
        id: 'price-alert',
        name: 'Price Changes',
        description: 'Triggered when vehicle prices change',
        type: 'public',
        url: '',
        enabled: true,
        messageTemplate: ' **Price Update** for {make} {model}\n**Old Price:** ${old_price}  **New Price:** ${new_price}\n[View Posting]({posting_url})',
        exampleData: {
          make: 'Audi',
          model: 'RS6',
          old_price: '85,000',
          new_price: '79,500',
          posting_url: 'https://carspot.site/cars/789'
        }
      },
      {
        id: 'sold',
        name: 'Vehicle Sold',
        description: 'Triggered when a vehicle is marked as sold',
        type: 'public',
        url: '',
        enabled: true,
        messageTemplate: ' **{make} {model}** has been sold!\n**Seller:** {username}\n**Final Price:** ${price}',
        exampleData: {
          make: 'Porsche',
          model: '911',
          username: 'SportsCarDealer',
          price: '95,000'
        }
      },

      // Private Webhooks
      {
        id: 'new-user',
        name: 'New User Registration',
        description: 'Triggered when a new user joins CarSpot',
        type: 'private',
        url: '',
        enabled: true,
        messageTemplate: ' **{username}** has joined CarSpot!',
        exampleData: {
          username: 'NewUser123'
        }
      },
      {
        id: 'dealer-application',
        name: 'Dealer Application',
        description: 'Triggered when someone applies to become a dealer',
        type: 'private',
        url: '',
        enabled: true,
        messageTemplate: ' **{username}** has applied to become a dealer!\n[Review Application]({application_url})',
        exampleData: {
          username: 'AspiringDealer',
          application_url: 'https://carspot.site/admin/dealer-applications/123'
        }
      },
      {
        id: 'dealer-payment',
        name: 'Dealer Payment',
        description: 'Triggered when a dealer pays their membership dues',
        type: 'private',
        url: '',
        enabled: true,
        messageTemplate: ' **{username}** has paid their dealer membership dues!\n**Amount:** ${amount}\n**Plan:** {plan}',
        exampleData: {
          username: 'PremiumDealer',
          amount: '$99.99',
          plan: 'Premium Monthly'
        }
      },

      // System Webhooks
      {
        id: 'tickets',
        name: 'Support Tickets',
        description: 'Triggered for ticket updates (creation, assignment, resolution)',
        type: 'system',
        url: '',
        enabled: true,
        messageTemplate: ' **{action}**\n**User:** {username}\n**Subject:** {subject}\n[View Ticket]({ticket_url})',
        exampleData: {
          action: 'New ticket submitted',
          username: 'User123',
          subject: 'Payment issue',
          ticket_url: 'https://carspot.site/admin/tickets/123'
        }
      },
      {
        id: 'reports',
        name: 'System Reports',
        description: 'Triggered for report updates (creation, investigation, resolution)',
        type: 'system',
        url: '',
        enabled: true,
        messageTemplate: ' **{action}**\n**Reporter:** {username}\n**Type:** {report_type}\n[View Report]({report_url})',
        exampleData: {
          action: 'New report submitted',
          username: 'Reporter456',
          report_type: 'Inappropriate content',
          report_url: 'https://carspot.site/admin/reports/456'
        }
      }
    ];

    setWebhooks(defaultWebhooks);
  };

  const updateWebhook = async (id: string, updates: Partial<WebhookConfig>) => {
    console.log('Updating webhook:', id, updates);
    
    // Update local state immediately for UI responsiveness
    setWebhooks(prev => prev.map(webhook => 
      webhook.id === id ? { ...webhook, ...updates } : webhook
    ));

    // Save to database
    try {
      const payload: any = {
        webhook_id: id,
        url: updates.url || '',
        enabled: updates.enabled !== undefined ? updates.enabled : true
      };
      
      // Only include message_template if it's being updated
      if (updates.messageTemplate !== undefined) {
        payload.message_template = updates.messageTemplate;
      }
      
      console.log('Saving to database:', payload);
      
      const response = await fetch('/api/webhooks.php?action=save_config', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      });

      if (response.ok) {
        const result = await response.json();
        console.log('Webhook saved successfully:', result);
      } else {
        const errorText = await response.text();
        console.error('Failed to save webhook config:', response.status, response.statusText, errorText);
        setError(`Failed to save webhook: ${response.status} ${response.statusText}`);
      }
    } catch (error) {
      console.error('Error saving webhook config:', error);
      setError(`Error saving webhook: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  };

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
    // Show temporary success message
    const button = document.querySelector(`[data-copy="${text}"]`) as HTMLElement;
    if (button) {
      const originalText = button.innerHTML;
      button.innerHTML = '<CheckCircle className="h-4 w-4 text-green-500" />';
      setTimeout(() => {
        button.innerHTML = originalText;
      }, 2000);
    }
  };

  const testWebhook = async (webhook: WebhookConfig) => {
    setTesting(true);
    setTestResults([]);
    
    try {
      // Test the webhook with example data
      const response = await fetch('/api/test-webhook.php', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          webhookId: webhook.id,
          webhookUrl: webhook.url,
          data: webhook.exampleData,
          template: webhook.messageTemplate || '' // Send empty string if template is null/undefined
        })
      });

      const result = await response.json();
      
      setTestResults(prev => [...prev, {
        webhookId: webhook.id,
        success: result.success,
        message: result.message,
        discordResponse: result.discordResponse,
        timestamp: new Date().toLocaleTimeString()
      }]);

    } catch (error) {
      setTestResults(prev => [...prev, {
        webhookId: webhook.id,
        success: false,
        message: 'Failed to test webhook',
        error: error instanceof Error ? error.message : 'Unknown error',
        timestamp: new Date().toLocaleTimeString()
      }]);
    } finally {
      setTesting(false);
    }
  };

  const testAllWebhooks = async () => {
    setShowTestModal(true);
    setTestResults([]);
    
    for (const webhook of webhooks.filter(w => w.enabled && w.url)) {
      await testWebhook(webhook);
      // Small delay between tests
      await new Promise(resolve => setTimeout(resolve, 500));
    }
  };

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'public': return <Globe className="h-4 w-4 text-blue-500" />;
      case 'private': return <Lock className="h-4 w-4 text-yellow-500" />;
      case 'system': return <Settings className="h-4 w-4 text-purple-500" />;
      default: return <Webhook className="h-4 w-4 text-gray-500" />;
    }
  };

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'public': return 'border-blue-500 bg-blue-50 dark:bg-blue-900/20';
      case 'private': return 'border-yellow-500 bg-yellow-50 dark:bg-yellow-900/20';
      case 'system': return 'border-purple-500 bg-purple-50 dark:bg-purple-900/20';
      default: return 'border-gray-500 bg-gray-50 dark:bg-gray-900/20';
    }
  };

  const getTypeLabel = (type: string) => {
    switch (type) {
      case 'public': return 'Public';
      case 'private': return 'Private';
      case 'system': return 'System';
      default: return 'Unknown';
    }
  };

  const getDefaultTemplate = (webhookId: string) => {
    const defaultTemplates: { [key: string]: string } = {
      'new-postings': ' **{username}** posted a new vehicle!\n**{make} {model}** - ${price}\n[View Posting]({posting_url})',
      'new-featured': ' **{username}** has a new featured vehicle!\n**{make} {model}** - ${price}\n[View Featured Posting]({posting_url})',
      'price-alert': ' **Price Update** for {make} {model}\n**Old Price:** ${old_price}  **New Price:** ${new_price}\n[View Posting]({posting_url})',
      'sold': ' **{make} {model}** has been sold!\n**Seller:** {username}\n**Final Price:** ${price}',
      'new-user': ' **{username}** has joined CarSpot!',
      'dealer-application': ' **{username}** has applied to become a dealer!\n[Review Application]({application_url})',
      'dealer-payment': ' **{username}** has paid their dealer membership dues!\n**Amount:** ${amount}\n**Plan:** {plan}',
      'tickets': ' **{action}**\n**User:** {username}\n**Subject:** {subject}\n**Status:** {status}\n[View Ticket]({ticket_url})',
      'reports': ' **{action}**\n**Reporter:** {username}\n**Type:** {report_type}\n**Content:** {content}\n[View Report]({report_url})'
    };
    return defaultTemplates[webhookId] || '**Default Template**\nThis is the default message for {webhook_id}';
  };

  if (loading) {
    return (
      <div className="space-y-6">
        <div className="flex items-center justify-center py-12">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
            <p className="mt-4 text-gray-600 dark:text-gray-400">Loading webhook configurations...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white">Webhook Management</h2>
          <p className="text-gray-600 dark:text-gray-400">Configure Discord webhooks for system events</p>
        </div>
        <div className="flex space-x-2">
          <button
            onClick={loadWebhooks}
            className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
            title="Refresh webhook configurations"
          >
            <RefreshCw className="h-4 w-4 mr-2" />
            Refresh
          </button>
          <button
            onClick={testAllWebhooks}
            disabled={testing || !webhooks.some(w => w.enabled && w.url)}
            className="flex items-center px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <TestTube className="h-4 w-4 mr-2" />
            {testing ? 'Testing...' : 'Test All Webhooks'}
          </button>
        </div>
      </div>

      {/* Error Display */}
      {error && (
        <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
          <div className="flex items-center">
            <AlertCircle className="h-5 w-5 text-red-500 mr-2" />
            <div>
              <h3 className="text-sm font-medium text-red-800 dark:text-red-200">Error Loading Webhooks</h3>
              <p className="text-sm text-red-700 dark:text-red-300 mt-1">{error}</p>
              <button
                onClick={loadWebhooks}
                className="mt-2 text-sm text-red-600 dark:text-red-400 hover:text-red-800 dark:hover:text-red-200 underline"
              >
                Try again
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Webhook Categories */}
      {['public', 'private', 'system'].map(type => (
        <div key={type} className="space-y-4">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center">
            {getTypeIcon(type)}
            <span className="ml-2">{getTypeLabel(type)} Webhooks</span>
          </h3>
          
          <div className="grid gap-4">
            {webhooks
              .filter(webhook => webhook.type === type)
              .map(webhook => (
                <div
                  key={webhook.id}
                  className={`p-6 border rounded-lg ${getTypeColor(type)} dark:bg-gray-800 dark:border-gray-700`}
                >
                  <div className="flex items-start justify-between mb-4">
                    <div className="flex-1">
                      <div className="flex items-center mb-2">
                        <h4 className="text-lg font-medium text-gray-900 dark:text-white">
                          #{webhook.name}
                        </h4>
                        <span className={`ml-2 inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                          webhook.enabled 
                            ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
                            : 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'
                        }`}>
                          {webhook.enabled ? 'Enabled' : 'Disabled'}
                        </span>
                      </div>
                      <p className="text-gray-600 dark:text-gray-400 mb-4">
                        {webhook.description}
                      </p>
                    </div>
                    
                    <div className="flex items-center space-x-2">
                      <button
                        onClick={() => testWebhook(webhook)}
                        disabled={testing || !webhook.enabled || !webhook.url}
                        className="p-2 text-green-600 hover:text-green-700 disabled:opacity-50"
                        title="Test this webhook"
                      >
                        <TestTube className="h-4 w-4" />
                      </button>
                    </div>
                  </div>

                  {/* Webhook URL */}
                  <div className="mb-4">
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Discord Webhook URL
                    </label>
                    <div className="flex">
                      <input
                        type="url"
                        value={webhook.url}
                        onChange={(e) => updateWebhook(webhook.id, { url: e.target.value })}
                        placeholder="https://discord.com/api/webhooks/..."
                        className="flex-1 px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-l-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:text-white"
                      />
                      <button
                        onClick={() => copyToClipboard(webhook.url)}
                        data-copy={webhook.url}
                        className="px-3 py-2 border border-l-0 border-gray-300 dark:border-gray-600 rounded-r-md bg-gray-50 dark:bg-gray-600 hover:bg-gray-100 dark:hover:bg-gray-500"
                        title="Copy URL"
                      >
                        <Copy className="h-4 w-4" />
                      </button>
                    </div>
                  </div>

                  {/* Message Template */}
                  <div className="mb-4">
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      Message Template
                    </label>
                    <textarea
                      value={webhook.messageTemplate}
                      onChange={(e) => updateWebhook(webhook.id, { messageTemplate: e.target.value })}
                      rows={3}
                      className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:text-white"
                      placeholder="Enter Discord message template with {variables}"
                    />
                    <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                      Use {'{variable_name}'} for dynamic content. Available variables depend on the event type.
                    </p>
                  </div>

                  {/* Example Preview */}
                  <div className="bg-gray-100 dark:bg-gray-700 rounded-lg p-3">
                    <h5 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">Example Message:</h5>
                    <div className="text-sm text-gray-600 dark:text-gray-400 whitespace-pre-wrap">
                      {(webhook.messageTemplate || getDefaultTemplate(webhook.id)).replace(/\{(\w+)\}/g, (match, variable) => {
                        return webhook.exampleData[variable] || match;
                      })}
                    </div>
                  </div>

                  {/* Enable/Disable Toggle */}
                  <div className="mt-4 flex items-center">
                    <input
                      type="checkbox"
                      id={`enabled-${webhook.id}`}
                      checked={webhook.enabled}
                      onChange={(e) => updateWebhook(webhook.id, { enabled: e.target.checked })}
                      className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                    />
                    <label htmlFor={`enabled-${webhook.id}`} className="ml-2 text-sm text-gray-700 dark:text-gray-300">
                      Enable this webhook
                    </label>
                  </div>
                </div>
              ))}
          </div>
        </div>
      ))}

      {/* Test Results Modal */}
      {showTestModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white dark:bg-gray-800 rounded-lg p-6 max-w-4xl w-full mx-4 max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-medium text-gray-900 dark:text-white">Webhook Test Results</h3>
              <button
                onClick={() => setShowTestModal(false)}
                className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
              >
                <span className="sr-only">Close</span>
                <svg className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            
            {testResults.length === 0 ? (
              <div className="text-center py-8">
                <TestTube className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-500 dark:text-gray-400">No test results yet. Run tests to see results.</p>
              </div>
            ) : (
              <div className="space-y-4">
                {testResults.map((result, index) => (
                  <div
                    key={index}
                    className={`p-4 rounded-lg border ${
                      result.success 
                        ? 'border-green-200 bg-green-50 dark:bg-green-900/20 dark:border-green-800'
                        : 'border-red-200 bg-red-50 dark:bg-red-900/20 dark:border-red-800'
                    }`}
                  >
                    <div className="flex items-center justify-between mb-2">
                      <div className="flex items-center">
                        {result.success ? (
                          <CheckCircle className="h-5 w-5 text-green-500 mr-2" />
                        ) : (
                          <AlertCircle className="h-5 w-5 text-red-500 mr-2" />
                        )}
                        <span className="font-medium text-gray-900 dark:text-white">
                          {webhooks.find(w => w.id === result.webhookId)?.name}
                        </span>
                      </div>
                      <span className="text-sm text-gray-500 dark:text-gray-400">
                        {result.timestamp}
                      </span>
                    </div>
                    
                    <p className={`text-sm ${
                      result.success ? 'text-green-700 dark:text-green-300' : 'text-red-700 dark:text-red-300'
                    }`}>
                      {result.message}
                    </p>
                    
                    {result.discordResponse && (
                      <div className="mt-2 p-2 bg-gray-100 dark:bg-gray-700 rounded text-xs">
                        <strong>Discord Response:</strong> {JSON.stringify(result.discordResponse, null, 2)}
                      </div>
                    )}
                    
                    {result.error && (
                      <div className="mt-2 p-2 bg-red-100 dark:bg-red-900/20 rounded text-xs text-red-700 dark:text-red-300">
                        <strong>Error:</strong> {result.error}
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default WebhookManagement;
