// Review service - Connected to PHP API backend
// This service will be updated to use PHP endpoints when they're created

export interface Review {
  id?: number;
  reviewer_id: number;
  reviewed_user_id: number;
  transaction_id?: number;
  rating: number;
  title: string;
  comment: string;
  review_type: string;
  is_verified: boolean;
  status: string;
  reviewer_name: string;
  reviewer_discord: string;
  responses?: ReviewResponse[];
  created_at?: Date;
  updated_at?: Date;
}

export interface ReviewResponse {
  id?: number;
  review_id: number;
  responder_id: number;
  response: string;
  responder_name: string;
  created_at?: Date;
  updated_at?: Date;
}

export interface UserProfile {
  id?: number;
  username: string;
  email: string;
  discord: string;
  bio?: string;
  avatar_url?: string;
  created_at?: Date;
  updated_at?: Date;
}

export interface UserStats {
  total_reviews: number;
  average_rating: number;
  name: string;
  discord: string;
  is_dealer: boolean;
  company_name?: string;
  rating: number;
  total_awards: number;
  profile: {
    bio?: string;
    avatar_url?: string;
    specialties: string[];
    certifications: string[];
    experience_years: number;
    website?: string;
    member_since?: Date;
  };
  review_stats: {
    total_reviews: number;
    average_rating: number;
    rating_distribution: { [key: number]: number };
    reviews_by_type: { [key: string]: number };
    total_buyer_reviews: number;
    total_seller_reviews: number;
  };
  reviews: Review[];
  awards: any[];
}

export const ReviewService = {
  // Get reviews for a user
  async getUserReviews(userId: number): Promise<Review[]> {
    try {
      const response = await fetch(`/api/reviews.php?user_id=${userId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting user reviews:', error);
      throw new Error('Failed to get user reviews');
    }
  },

  // Create a new review
  async createReview(review: Omit<Review, 'id' | 'created_at' | 'updated_at'>): Promise<Review> {
    try {
      const response = await fetch('/api/reviews.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(review),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const result = await response.json();
      return { ...review, id: result.id, created_at: new Date(), updated_at: new Date() };
    } catch (error) {
      console.error('Error creating review:', error);
      throw new Error('Failed to create review');
    }
  },

  // Update a review
  async updateReview(id: number, updates: Partial<Review>): Promise<Review | null> {
    try {
      const response = await fetch('/api/reviews.php', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ id, ...updates }),
      });
      
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error updating review:', error);
      throw new Error('Failed to update review');
    }
  },

  // Delete a review
  async deleteReview(id: number): Promise<boolean> {
    try {
      const response = await fetch(`/api/reviews.php?id=${id}`, {
        method: 'DELETE',
      });
      
      if (!response.ok) {
        if (response.status === 404) return false;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error deleting review:', error);
      throw new Error('Failed to delete review');
    }
  },

  // Get review responses
  async getReviewResponses(reviewId: number): Promise<ReviewResponse[]> {
    try {
      const response = await fetch(`/api/reviews.php?action=responses&review_id=${reviewId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting review responses:', error);
      throw new Error('Failed to get review responses');
    }
  },

  // Add response to a review
  async addReviewResponse(response: Omit<ReviewResponse, 'id' | 'created_at' | 'updated_at'>): Promise<ReviewResponse> {
    try {
      const responseData = await fetch('/api/reviews.php?action=add_response', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(response),
      });
      
      if (!responseData.ok) {
        throw new Error(`HTTP error! status: ${responseData.status}`);
      }
      
      const result = await responseData.json();
      return { ...response, id: result.id, created_at: new Date(), updated_at: new Date() };
    } catch (error) {
      console.error('Error adding review response:', error);
      throw new Error('Failed to add review response');
    }
  },

  // Get user profile
  async getUserProfile(userId: number): Promise<UserProfile | null> {
    try {
      const response = await fetch(`/api/users.php?id=${userId}`);
      
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting user profile:', error);
      throw new Error('Failed to get user profile');
    }
  },

  // Get user statistics
  async getUserStats(userId: number): Promise<UserStats> {
    try {
      const response = await fetch(`/api/reviews.php?action=stats&user_id=${userId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting user stats:', error);
      throw new Error('Failed to get user stats');
    }
  }
};
