// Dealer service - Connected to PHP API backend

export interface DealerAccount {
  id?: number;
  name: string;
  company_name: string;
  phone: string;
  discord: string;
  website?: string;
  expected_monthly_listings?: string;
  logo_url?: string;
  status: 'pending' | 'active' | 'suspended';
  created_at?: Date;
  updated_at?: Date;
}

export interface DealerUserRole {
  id?: number;
  dealer_account_id: number;
  user_id: number;
  role: 'owner' | 'manager' | 'staff';
  permissions: string[];
  created_at?: Date;
  updated_at?: Date;
  joined_at?: Date;
}

export interface DealerInvitation {
  id?: number;
  dealer_account_id: number;
  discord: string;
  role: 'manager' | 'staff';
  status: 'pending' | 'accepted' | 'declined';
  expires_at: Date;
  created_at?: Date;
  updated_at?: Date;
}

export interface UserDealerRole {
  is_dealer: boolean;
  dealer_account_id: number | null;
  company_name: string | null;
  role: string | null;
}

export const DealerService = {
  // Check if a user is a dealer or dealer team member
  async checkUserDealerRole(userId: number): Promise<UserDealerRole> {
    try {
      const response = await fetch(`/api/dealers.php?action=check_user_role&user_id=${userId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error checking user dealer role:', error);
      // Default to not being a dealer if there's an error
      return {
        is_dealer: false,
        dealer_account_id: null,
        company_name: null,
        role: null
      };
    }
  },

  // Create a new dealer account
  async createDealerAccount(dealer: Omit<DealerAccount, 'id' | 'created_at' | 'updated_at'>): Promise<DealerAccount> {
    try {
      const response = await fetch('/api/dealers.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(dealer),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const result = await response.json();
      return { ...dealer, id: result.id, created_at: new Date(), updated_at: new Date() };
    } catch (error) {
      console.error('Error creating dealer account:', error);
      throw new Error('Failed to create dealer account');
    }
  },

  // Get dealer account by ID
  async getDealerAccount(id: number): Promise<DealerAccount | null> {
    try {
      const response = await fetch(`/api/dealers.php?id=${id}`);
      
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting dealer account:', error);
      throw new Error('Failed to get dealer account');
    }
  },

  // Update dealer account
  async updateDealerAccount(id: number, updates: Partial<DealerAccount>): Promise<DealerAccount | null> {
    try {
      const response = await fetch('/api/dealers.php', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ id, ...updates }),
      });
      
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const result = await response.json();
      return await this.getDealerAccount(id);
    } catch (error) {
      console.error('Error updating dealer account:', error);
      throw new Error('Failed to update dealer account');
    }
  },

  // Get all dealer accounts
  async getDealerAccounts(): Promise<DealerAccount[]> {
    try {
      const response = await fetch('/api/dealers.php');
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting dealer accounts:', error);
      throw new Error('Failed to get dealer accounts');
    }
  },

  // Add user to dealer team
  async addUserToDealer(dealerId: number, userId: number, role: 'owner' | 'manager' | 'staff'): Promise<DealerUserRole> {
    try {
      const response = await fetch('/api/dealers.php?action=add_user', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ dealer_id: dealerId, user_id: userId, role }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error adding user to dealer:', error);
      throw new Error('Failed to add user to dealer');
    }
  },

  // Get dealer team members
  async getDealerTeam(dealerId: number): Promise<DealerUserRole[]> {
    try {
      const response = await fetch(`/api/dealers.php?action=team&dealer_id=${dealerId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting dealer team:', error);
      throw new Error('Failed to get dealer team');
    }
  },

  // Send invitation to join dealer team
  async sendInvitation(invitation: Omit<DealerInvitation, 'id' | 'created_at' | 'updated_at'>): Promise<DealerInvitation> {
    try {
      const response = await fetch('/api/dealers.php?action=invite', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(invitation),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error sending invitation:', error);
      throw new Error('Failed to send invitation');
    }
  },

  // Get pending invitations
  async getPendingInvitations(dealerId: number): Promise<DealerInvitation[]> {
    try {
      const response = await fetch(`/api/dealers.php?action=pending_invitations&dealer_id=${dealerId}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error getting pending invitations:', error);
      throw new Error('Failed to get pending invitations');
    }
  },

  // Accept invitation
  async acceptInvitation(invitationId: number, userId: number): Promise<boolean> {
    try {
      const response = await fetch('/api/dealers.php?action=accept_invitation', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ invitation_id: invitationId, user_id: userId }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error accepting invitation:', error);
      throw new Error('Failed to accept invitation');
    }
  },

  // Decline invitation
  async declineInvitation(invitationId: number): Promise<boolean> {
    try {
      const response = await fetch('/api/dealers.php?action=decline_invitation', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ invitation_id: invitationId }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error declining invitation:', error);
      throw new Error('Failed to decline invitation');
    }
  },

  // Helper function to get permissions for a role
  getPermissionsForRole(role: 'owner' | 'manager' | 'staff'): string[] {
    switch (role) {
      case 'owner':
        return ['read', 'write', 'delete', 'manage_team', 'manage_settings'];
      case 'manager':
        return ['read', 'write', 'manage_team'];
      case 'staff':
        return ['read', 'write'];
      default:
        return ['read'];
    }
  },

  // Search for users by username or Discord
  async searchUsers(query: string): Promise<any[]> {
    try {
      const response = await fetch(`/api/dealers.php?action=search_users&query=${encodeURIComponent(query)}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return await response.json();
    } catch (error) {
      console.error('Error searching users:', error);
      throw new Error('Failed to search users');
    }
  },

  // Add user directly to dealer team
  async addUserToTeam(dealerId: number, userId: number, role: 'manager' | 'staff'): Promise<boolean> {
    try {
      const response = await fetch('/api/dealers.php?action=add_team_member', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ 
          dealer_account_id: dealerId, 
          user_id: userId, 
          role: role 
        }),
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      return true;
    } catch (error) {
      console.error('Error adding user to team:', error);
      throw new Error('Failed to add user to team');
    }
  }
};
